<template>
  <span
    ref="activator"
    class="vu-popover__activator"
    @click="innerShow = !innerShow"
    v-bind="$attrs"
    v-click-outside="{ handler: onClickOutside }">
    <slot />
  </span>
  <Teleport :to="target" v-if="open || persistent" v-show="open">
    <transition
      :name="animated ? 'fade' : ''"
      mode="out-in">
      <div class="mask popover-mask" v-if="innerShow && overlay"
        ref="overlay"
        @wheel.prevent="onClickOutside"
        @touchstart="(e) => onClickOutside(e, true)">
      </div>
    </transition>
    <vu-tooltip
      ref="tooltip"
      :arrow="arrow"
      :prerender="!positioned"
      :show="innerShow || show"
      :type="type"
      :side="innerSide || side"
      :class="contentClass"
      :style="contentStyle"
      :animated="animated"
      @update:show="open = false">
      <template #arrow="{ side }">
        <slot name="arrow" :side="innerSide || side" :shift="shifted" />
      </template>
      <template #title="{ side }">
        <slot name="title" :side="innerSide || side">
          <template v-if="title">
            {{ title }}
          </template>
        </slot>
      </template>
      <template #default>
        <slot name="body" />
      </template>
    </vu-tooltip>
  </Teleport>
</template>

<script>

import showable from '../mixins/showable';
import detachable from '../mixins/detachable';
import VuTooltip from './vu-tooltip.vue';
import { positions, checkPosition, getNextPosition } from './vu-popover-positions';
import { setPosition } from '../utils/positionable';

export default {
  name: 'vu-popover',
  mixins: [showable, detachable],
  expose: ['updatePosition'],
  emits: ['unpositionable'],
  components: { VuTooltip },
  props: {
    type: {
      type: String,
      default: 'popover',
    },
    side: {
      type: String,
      default: 'bottom',
    },
    arrow: {
      type: Boolean,
      default: false,
    },
    shift: {
      type: Boolean,
      default: false,
    },
    animated: {
      type: Boolean,
      default: true,
    },
    overlay: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: () => '',
    },
    persistent: {
      type: Boolean,
      default: false,
    },
    positions: {
      type: Array,
      required: false,
      default: () => positions,
    },
    getNextPosition: {
      type: Function,
      required: false,
      default: getNextPosition,
    },
    checkPosition: {
      type: Function,
      required: false,
      default: checkPosition,
    },
  },
  data: () => ({
    open: false,
    resizeObs: null,
    intersectionObs: null,
    setPositionBound: null,
    shifted: false,
    positioned: false,
    positionAttempts: [],
    scrollableAncestors: [], // put in positionable
    innerSide: '',
  }),
  watch: {
    innerShow: {
      immediate: true,
      async handler(val) {
        await new Promise((r) => setTimeout(r, 10));
        if (val) {
          this.open = true;
          this.positioned = false;
          await this.$nextTick();
          this.setPositionBound();

          this.intersectionObs.observe(this.$refs.tooltip.$el);
          this.listenScrolls();
        } else {
          this.open = false;
          if (this.$refs.tooltip) {
            this.resizeObs.disconnect();
          }
          this.stopScrollListening();
        }
      },
    },
    innerSide: {
      handler() {
        this.updatePosition();
      },
    },
    attach() {
      if (this.innerShow) {
        this.updatePosition();
      }
    },
  },
  created() {
    // this.setPositionBound = throttle(this.setPosition.bind(this), 10);
    this.setPositionBound = this.setPosition.bind(this);
    this.resizeObs = new ResizeObserver(async () => {
      await this.$nextTick();
      this.setPositionBound();
    });
  },
  async mounted() {
    await this.$nextTick();
    let attempts = 0;
    const maxAttempts = 5;
    while (attempts < maxAttempts && (this.$refs.activator === undefined && this.$refs.tooltip === undefined)) {
      attempts++;
      // eslint-disable-next-line no-await-in-loop
      await this.$nextTick();
    }
    const { target, positionAttempts } = this;
    // eslint-disable-next-line object-curly-newline
    this.intersectionObs = new IntersectionObserver(([{ boundingClientRect: elementRect, root: targetRect, intersectionRatio, intersectionRect }]) => {
      this.intersectionObs.unobserve(this.$refs.tooltip.$el);
      // eslint-disable-next-line object-curly-newline
      if (this.checkPosition({ intersectionRatio, elementRect, targetRect, intersectionRect, positionAttempts })) {
        // Move to another position
        // fallback positions Props.
        const next = this.getNextPosition(this.innerSide || this.side, this.side, this.positions, this.positionAttempts);
        if (next === (this.innerSide || this.side)) {
          this.$emit('unpositionable');
          this.positioned = true;
          this.positionAttempts = [];
          return;
        }
        this.innerSide = next;
        this.positionAttempts.push(this.innerSide);
      } else {
        this.positioned = true;
        this.positionAttempts = [];
        this.resizeObs.observe(this.$refs.tooltip.$el);
        this.resizeObs.observe(this.target);
      }
    }, { root: target !== document.body ? target : undefined });
  },
  beforeUnmount() {
    try {
      this.innerShow = false;
      this.stopScrollListening();
      this.intersectionObs.disconnect();
      this.resizeObs.disconnect();
    // eslint-disable-next-line no-empty
    } catch (e) {
    }
  },
  methods: {
    listenScrolls() {
      // eslint-disable-next-line no-unreachable
      const scrollableAncestors = [];
      let parent = this.$refs.activator.parentElement;

      while (parent && (this.target.contains(parent) || parent === this.target)) {
        const { overflow } = window.getComputedStyle(parent);
        const props = overflow.split(' ');

        if (['auto', 'scroll'].some((prop) => props.includes(prop))) {
          scrollableAncestors.push(parent);
        }
        parent = parent.parentElement;
      }
      this.scrollableAncestors = scrollableAncestors;
      this.scrollableAncestors.forEach((el) => el.addEventListener('scroll', this.setPositionBound));
    },
    stopScrollListening() {
      this.scrollableAncestors.forEach((el) => el.removeEventListener('scroll', this.setPositionBound));
    },
    updatePosition() {
      this.setPositionBound();
      this.intersectionObs.observe(this.$refs.tooltip.$el);
    },
    setPosition() {
      const target = this.target.getBoundingClientRect();
      const activator = this.$refs.activator.getBoundingClientRect();
      const tooltip = this.$refs.tooltip.$el.getBoundingClientRect();
      const position = setPosition(
        (this.innerSide || this.side),
        activator,
        tooltip,
        target,
        this.target,
        this.shift,
      );
      this.shifted = position.shiftX;
      this.$refs.tooltip.$el.style.top = `${position.top}px`;
      this.$refs.tooltip.$el.style.left = `${position.left}px`;

      if (this.overlay) {
        this.$refs.overlay.style.top = `${ this.target === document.body ? document.scrollingElement.scrollTop : this.target.scrollTop }px`;
      }
    },
    // eslint-disable-next-line no-unused-vars
    onClickOutside(e, touchOverlay = false) {
      // ignore click outside when the dropdown isn't active
      if (!this.innerShow) return;

      const { target } = e;
      if (touchOverlay) {
        e.preventDefault();
      }

      // ignore click outside when the event come from the dropdown
      if (
        this.$refs.tooltip 
        && (target === this.$refs.tooltip.$el || this.$refs.tooltip.$el.contains(target))
      ) {
        return;
      }
      this.innerShow = false;
    },
  },
};
</script>

<style lang="scss" scoped>
  .vu-popover__activator {
    display: inline-block;
    position: relative;
  }

  .popover-mask {
    background-color: rgba(0, 0, 0, 0.5);
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
      opacity: 0;
  }

  .fade-enter-from, .fade-enter-active {
    transition-delay: .3s;
  }
</style>
