<template>
  <template v-if="isVideo">
    <div class="vu-media-upload-preview__video-container" :style="videoSizer">
      <video class="vu-media-upload-preview" :src="src" :controls="videoControls"></video>
    </div>
  </template>
  <vu-image v-else-if="!isVideo" class="vu-media-upload-preview" :aspectRatio="displayRatio" :src="src" contain style="height: 100%;">
    <div class="vu-media-upload-preview__delete-icon" @click="$emit('delete')">
      <vu-icon-btn :icon="deleteIcon"></vu-icon-btn>
    </div>
  </vu-image>
  <vu-spinner v-else-if="loading"></vu-spinner>
  <div v-if="isVideo" class="vu-media-upload-preview__delete-icon" @click="$emit('delete')">
    <vu-icon-btn :icon="deleteIcon"></vu-icon-btn>
  </div>
</template>
<script>
export default {
  name: 'vu-media-upload-preview',
  computed: {
    videoSizer() {
      const [width, height] = this.displayRatio?.replace(',', '').split('/');
      const ratio = Number(width) / Number(height);
      return ratio ? { paddingBottom: `${ (1 / ratio) * 100 }%` } : undefined;
    },
  },
  props: {
    deleteIcon: {
      type: String,
      default: () => 'trash',
    },
    src: {
      type: String,
      required: true,
    },
    isVideo: {
      type: Boolean,
    },
    videoControls: {
      type: Boolean,
      required: false,
    },
    displayRatio: {
      type: String,
      default: () => '16 / 9',
    },
  },
  emits: ['delete'],
};
</script>
<style lang="scss">
@import '../styles/variables';

.vu-media-upload-preview.vu-image {
  --vu-image-background-fill-color: #{$grey-1};
}
</style>
<style scoped lang="scss">
@import '../styles/variables';
