  background-color: $grey-1;
}

.vu-media-upload-preview__video-container {
  position: relative;
  height: 0;
  width: 100%;
  
  video {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.vu-media-upload-preview__delete-icon {
  position: absolute;
  top: 0;
  right: 0;
  background: $grey-1;
  border-radius: 0px 3px 0px 4px;
  z-index: 2;
}
</style>
