// TODO TS extend Inputable.
import { getListenersFromAttrs } from '../utils/vue';

export default {
  emits: ['update:modelValue', 'boundary-change'],
  props: {
    modelValue: {
      type: [Array],
      default: () => [],
    },
    min: {
      type: [Number, Date],
      default: () => -2208988800000, // 1900-01-01Z00:00:00.000Z
    },
    max: {
      type: [Number, Date],
      default: () => 4102444799999, // 2099-12-31T23:59:59.999Z
    },
  },
  data: () => ({
    getListenersFromAttrs,
  }),
  watch: {
    min: {
      handler(v) {
        this.checkBoundary(v, 'min');
      },
      immediate: true,
    },
    max: {
      handler(v) {
        this.checkBoundary(v, 'max');
      },
      immediate: true,
    },
  },
  methods: {
    checkBoundary(value, boundary) {
      if (!this.value) return;
      const event = this.getListenersFromAttrs(this.$attrs)['boundary-change'] ? 'boundary-change' : 'update:modelValue';
      if (
        (['min'].includes(boundary) && this.value < value)
        || (['max'].includes(boundary) && this.value > value)
      ) {
        if (value) { // we are manipulating a date, we must input a date
          this.$emit(event, event === 'update:modelValue' ? new Date(value) : { boundary, value: new Date(value) });
        } else {
          this.$emit(event, event === 'update:modelValue' ? value : { boundary, value });
        }
      }
    },
  },
};
