<template>
  <ul class="vu-select-options" :class="{ 'select-options--multiple': multiple, 'select-options--user': user }">
      <li
        v-for="item in options"
        :key="`${item.id || uuidv4()}`"
        :class="{ 'option--selected': (item.selected || selected.includes(item)) }"
        :disabled="item.disabled"
        @click="$emit('click-item', item)"
      >
        <template v-if="user">
          <div class="flex items-center">
            <vu-user-picture size="small" :id="item.value" :src="item.src"
            /><span class="option__text">{{ item.text || item.label }}</span>
          </div>
        </template>
        <slot v-else :item="item">
          <vu-icon v-if="item.fonticon" :icon="item.fonticon" />
          <span class="option__text">{{ item.text || item.label }}</span>
        </slot>
    </li>
  </ul>
</template>

<script>
import { uuidv4 } from '../utils/guid';

export default {
  name: 'vu-select-options',
  props: {
    options: {
      type: Array,
      required: true,
    },
    multiple: {
      type: Boolean,
      required: false,
    },
    user: {
      type: Boolean,
      required: false,
    },
    selected: {
      type: Array,
      required: true,
    },
    zIndex: {
      type: Number,
      required: false,
    },
  },
  data: () => ({
    uuidv4,
  }),
};
</script>
