// eslint-disable-next-line import/no-extraneous-dependencies
import { createVNode, render } from 'vue';
import Spinner from '../components/vu-spinner.vue';

const show = (element, options, vnode) => {
  const spinner = createVNode(Spinner, { mask: true });
  render(spinner, vnode.el);

  // eslint-disable-next-line no-param-reassign
  element.spinner = spinner;
  if (spinner && typeof options.value === 'string') {
    const { component } = spinner;
    spinner.props.text = options.value;
    if (component) { component.props.text = options.value; }
  }
  element.classList.add('masked');
};

const hide = (element, options, vnode) => {
  if (element.spinner) {
    render(null, vnode.el);
    // eslint-disable-next-line no-param-reassign
    element.spinner = null;
    element.classList.remove('masked');
  }
};

export default {
  mounted(element, options, vnode) {
    if (options.value) {
      show(element, options, vnode);
    }
  },
  updated(element, options, vnode) {
    if (options.value === options.oldValue) {
      return;
    }
    (options.value ? show : hide)(element, options, vnode);
  },
  unmounted(element, options, vnode) {
    hide(element, options, vnode);
  },
};
