<template>
  <div class="vu-multiple-select form-group"
    :class="classes">
    <label class="control-label"
      v-if="label.length">{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <div v-click-outside="function () { close(); search = ''; }"
      ref="searchfield"
      :class="['select',
        'select-autocomplete', {
        'dropdown-visible': open,
        'select-disabled': disabled,
        'single-select': maxSelectable === 1,
      }]">
      <div @click="if (!(maxSelectable === 1 && values.length)) { $refs.input.focus(); openAndIntersect(); }"
        class="autocomplete-searchbox"
        :class="{
           'autocomplete-searchbox-active': open,
           'disabled': disabled,
           'autocomplete-searchbox--user': user,
           'autocomplete-searchbox--user-big-badges': user && userBigBadges,
          }">
        <template v-if="user">
          <div
            v-for="v in value"
            :key="`${uid}-tag-${v}`"
            class="vu-user-badge">
            <vu-user-picture
              :id="v.value"
              :src="v.src"
              size="tiny"
            ></vu-user-picture><span>{{ v.label }}</span><vu-icon-btn class="vu-user-badge__close" :icon="'close'" size="icon-smaller" @click="toggle(v)"></vu-icon-btn>
        </div>
        </template>
        <template v-else>
          <span
            v-for="v in value"
            :key="`${uid}-tag-${v}`"
            @click="toggle">
            <slot name="badge" :value="v">
              <vu-badge
                v-if="maxSelectable !== 1"
                closable
                @click.stop
                @close="toggle(v)">
                {{ v.label }}
              </vu-badge>
              <span v-else style="line-height: 30px;">{{ v.label }}</span>
            </slot>
          </span>
        </template>
        <input
          v-if="values.length < maxSelectable"
          ref="input"
          v-model="search"
          type="text"
          @input="({target}) => { if(!vuInputComposition) { target.composing = false } }"
          :placeholder="values.length && shortPlaceholder ? shortPlaceholder : placeholder"
          class="autocomplete-input"
          @click="openAndIntersect();">
      </div>
      <vu-icon-btn 
        v-if="maxSelectable === 1 && !user && value && value.length"
        icon="clear" 
        class="select__clear-icon" 
        @click.stop="toggle(value[0])"
      />
      <div v-if="open && searchLengthMet"
        ref="dropdown"
        class="select-dropdown"
        :class="[{ 'select-dropdown--no-results' : noResults, 'select-dropdown--dropup': top }, contentClass]"
        :style="[
          `height: ${ dropdownHeight }${ dropdownHeight !== 'auto' ? 'px' : '' }`,
          top ? `bottom: ${bottom}px` : '', 
          !positioned ? 'opacity: 0' : '',
          contentStyle]">
        <vu-scroller
          alwaysShow>
          <vu-select-options
            multiple
            :user="user"
            v-show="searchLengthMet && !loading"
            :selected="value"
            :options="innerOptions"
            @click-item="toggle">
            <template #default="{ item }">
              <slot :item="item" />
            </template>
          </vu-select-options>
          <template v-if="loading">
            <slot name="loading">
                <ul style="padding-top: 15px">
                  <li class="message">
                    <vu-spinner show></vu-spinner>
                  </li>
                </ul>
              </slot>
          </template>
          <template v-if="!loading && noResults">
            <slot name="noResults">
              <ul class="multiple-select__no-results">
                <li>{{ vuMultipleSelectLabels.noResults }} </li>
              </ul>
            </slot>
          </template>
        </vu-scroller>
      </div>
    </div>
    <span v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span v-if="helper.length"
      class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>

<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import loadable from '../mixins/loadable';
import disablable from '../mixins/disablable';
import detachable from '../mixins/detachable';

import { RegistrableInput } from '../mixins/registrable';
import { uuidv4 } from '../utils/guid';

export default {
  name: 'vu-multiple-select',
  inject: {
    vuMultipleSelectLabels: {
      default: () => ({
        noResults: 'No results.',
      }),
    },
    vuInputComposition: {
      default: false,
    },
  },
  mixins: [inputable, disablable, loadable, validatable, detachable, RegistrableInput],
  props: {
    modelValue: {
      type: [Object, String, Number, Array, Boolean, Date],
      default: () => '',
    },
    itemHeight: {
      type: Number,
      default: () => 38,
    },
    minSearchLength: {
      type: Number,
      default: () => 0,
    },
    shortPlaceholder: {
      type: String,
      required: false,
    },
    user: {
      type: Boolean,
      required: false,
    },
    userBigBadges: {
      type: Boolean,
      required: false,
    },
    maxVisible: {
      type: Number,
      default: () => 5,
    },
    maxSelectable: {
      type: Number,
      default: () => Infinity,
    },
    caseSensitive: {
      type: Boolean,
      default: false,
    },
    noLocalFiltering: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['search'],
  data: () => ({
    open: false,
    positioned: true,
    intxObs: null,
    search: '',
    bottom: 40,
    top: false,
    resizeObs: null,
    uid: uuidv4(),
  }),
  created() {
    this.resizeObs = new ResizeObserver((el) => {
      this.bottom = el[0].contentRect.height + 4;
    });
  },
  mounted() {
    if (this.$refs.searchfield) {
      this.resizeObs.observe(this.$refs.searchfield);
    }
    if (this.target) {
      // eslint-disable-next-line no-unused-vars
      this.intxObs = new IntersectionObserver((results) => {
        this.intxObs.unobserve(this.$refs.dropdown);
        const targetRect = this.target.getBoundingClientRect();
        const ddRect = this.$refs.dropdown.getBoundingClientRect();
        if (targetRect.bottom < ddRect.bottom) {
          this.top = true;
        }
        this.positioned = true;
      }, {
        root: this.target,
        threshold: 1,
      });
    }
  },
  watch: {
    search(v) { this.$emit('search', v); if (v && !this.open) this.openAndIntersect(); },
  },
  computed: {
    searchLengthMet() {
      return this.search.length >= this.minSearchLength;
    },
    innerOptions() {
      if (!this.searchLengthMet) return [];
      if (this.noLocalFiltering) return this.options;
      return this.caseSensitive ? this.options.filter((el) => (el.label.includes(this.search) || el.value.includes(this.search)))
        : this.options.filter((el) => (el.label.toLowerCase().includes(this.search.toLowerCase()) || el.value.toLowerCase().includes(this.search.toLowerCase())));
    },
    innerOptionsLength() {
      return this.innerOptions.length;
    },
    noResults() {
      return this.options && this.innerOptions.length === 0 && this.searchLengthMet;
    },
    values() { return (this.value || []).map((el) => el.value); },
    dropdownHeight() {
      if (this.noResults) return (this.$slots.noResults) ? 'auto' : this.itemHeight;
      if (this.innerOptionsLength > this.maxVisible) {
        return this.itemHeight * ((this.innerOptionsLength === this.maxVisible ? 0 : 0.5) + this.maxVisible);
      } 
      return (this.itemHeight * this.innerOptionsLength) + 1; // Remove scrollbar
    },
  },
  methods: {
    toggle(option) {
      if (option.disabled) {
        return;
      }

      this.search = '';
      const _value = (this.value || []);
      const index = _value.findIndex((el) => el.value === option.value);

      if (this.values.includes(option.value)) {
        if (this.maxSelectable === 1) {
          this.$emit('update:modelValue', []);
        } else {
          const values = _value.slice();
          values.splice(index, 1);
          this.$emit('update:modelValue', values);
        }
      } else if (this.maxSelectable === 1) {
        this.$emit('update:modelValue', [option]);
        this.close();
      } else {
        this.$emit('update:modelValue', _value.concat([option]));
      }
    },
    getOption(value) {
      return this.options.find((el) => el.value === value) || {};
    },
    close() {
      this.open = false;
      this.top = false;
    },
    async openAndIntersect() {
      if (this.searchLengthMet && !this.open) {
        if (this.target && ['scroll', 'auto', 'visible'].includes(window.getComputedStyle(this.target).overflowY)) {
          const targetRect = this.target.getBoundingClientRect();
          const fieldRect = this.$refs.searchfield.getBoundingClientRect();
          if (!this.top && (this.maxVisible + 0.5) * this.itemHeight 
            > (targetRect.bottom - fieldRect.bottom)) {
            this.top = true;
          }
          this.open = true;
        } else {
          this.open = true;
          this.positioned = false;
          await new Promise((r) => setTimeout(r, 10));
          await this.$nextTick();

          this.intxObs.observe(this.$refs.dropdown);
        }
      }
    },
    beforeUnmount() {
      this.intxObs.disconnect();
      delete this.intxObs;
    },
  },
};
</script>
