    padding-left: 9px;
  }

  .badge.badge-selectable:not(.badge-selected):not(.disabled):hover,
  .badge-selectable:focus {
    border-color: #77797C; // &grey-6;
    background-color: #E2E4E3; // &grey-3
  }

  .multiple-select__message {
    font-weight: bold;
    text-align: center;
    font-style: italic;
    cursor: inherit;
    margin: 0;
    padding: 0;
    background: none;
    list-style: none;
  }

  .dropdown-menu.dropdown-menu-root {
    border-bottom: none;
    box-shadow: none;
  }

  .autocomplete-searchbox input {
    .single-select &,
    &:first-child {
      width: calc(100% - 27px);
    }

    width: 72%;
  }

  .multiple-select {
    &__no-results{
      height: 38px;

      li {
        text-align: center;
        line-height: 38px;
      }
    }

  }
</style>
