// eslint-disable-next-line import/no-extraneous-dependencies
import { shallowReactive, reactive } from 'vue';
import uuid from '../../utils/uuid';
import MessageContainer from './vu-message-container.vue';

export function defineApi() {
  const namespaces = [];

  return {
    create(args) {
      const { target = 'main' } = args;
      if (!this._exists(target)) {
        throw new Error('Target namespace is unknown');
      }
      const message = {
        id: uuid(),
        bind: { 
          target,
          _dispose: false,
          show: true,
          ...args, 
        },
        dispose() { this.bind._dispose = true; },
      };
      this._messages[target].push(reactive(message));
      return message;
    },
    hide(instance) {
      const { target = 'main' } = instance.bind;
      this._messages[target].splice(this._messages[target].indexOf(instance), 1);
    },
    _exists(namespace) {
      return namespaces.includes(namespace);
    },
    _register(namespace) {
      namespaces.push(namespace);
      this._messages[namespace] = shallowReactive([]);
    },
    _messages: reactive([]),
  };
}

function bindApi(component, api) {
  const _component = { ...component, props: { ...component.props } };
  Object.assign(_component.props, {
    api: { type: Object, default: () => api },
  });
  return _component;
}

export function defineContainer(api) {
  return bindApi(MessageContainer, api);
}
