<template>
  <div class="vu-user-picture"
    :class="[size ? `vu-user-picture--${size}` : '', {
      'vu-user-picture--gutter': gutter, 
      'vu-user-picture--circle': circle,
      'vu-user-picture--clickable': clickable,
      'vu-user-picture--bg-inherit': inheritBackground 
    }]"
    @mouseover="() => { if(hoverable) hovered = true; }"
    @mouseleave="() => { if(hoverable) hovered = false; }">
    <div class="vu-user-picture-wrap"
      :style="[presence ? { background: 'inherit' } : '']">
      <div class="vu-user-picture__image"
        :style="{ 'background-image': `url(${_src})` }"></div>
      <div class="vu-user-picture__hover-mask"
        v-if="hovered" />
      <div
        v-if="size !== 'tiny'"
        class="vu-presence">
        <div :class="`vu-presence__indicator vu-presence__indicator--${presence}`"></div>
      </div>
    </div>
  </div>
</template>
<script>
import presenceStates from '../../model/presenceStates';

export default {
  name: 'vu-user-picture',
  inject: [
    'vuUserPictureSrcUrl',
  ],
  props: {
    size: {
      type: String,
      default: 'medium',
      validator: (size) => ['tiny', 'small', 'medium', 'medium-1', 'big', 'bigger', 'large', 'extra-large'].includes(size),
    },
    circle: {
      type: Boolean,
      default: true,
    },
    clickable: {
      type: Boolean,
      default: false,
    },
    gutter: {
      type: Boolean,
      default: false,
    },
    hoverable: {
      type: Boolean,
      default: false,
    },
    inheritBackground: {
      type: Boolean,
      default: true,
    },
    presence: {
      type: String,
      required: false,
      validator: (value) => (value ? presenceStates[value] !== undefined : true),
    },
    src: {
      type: String,
      required: false,
    },
    id: {
      type: String,
      required: false,
    },
  },
  data: () => ({
    presenceStates,
    hovered: false,
  }),
  watch: {
    hoverable: {
      // eslint-disable-next-line object-shorthand, func-names
      handler: function (val) {
        if (!val && this.hovered) {
          this.hovered = false;
        }
      },
    },
  },
  computed: {
    fonticon() {
      return this.presence && presenceStates[this.presence];
    },
    _src() {
      return this.vuUserPictureSrcUrl && this.id && !this.src ? `${this.vuUserPictureSrcUrl}/${this.id}` : this.src;
    },
  },
};

</script>
<style scoped lang="scss">
  @use "sass:math";
  @import '../../styles/_variables.scss';
  @import '../../styles/_avatar.scss';

  @mixin avatar-size($avatar-diameter, $indicator-wrap-diameter: 0, $indicator-diameter: 0) {
    $offset: math.div($indicator-wrap-diameter - $indicator-diameter, 2);
    width: $avatar-diameter;
    height: $avatar-diameter;

    .vu-presence {
      width: $indicator-wrap-diameter;
      height: $indicator-wrap-diameter;
      @if ($offset) {
        bottom: -$offset;
        right: -$offset;
      }
    }

    .vu-presence__indicator {
      width: $indicator-diameter;
      height: $indicator-diameter;
    }
  }

  @mixin avatar-size-with-gutter($avatar-diameter, $avatar-gutter-width, $avatar-outline-width) {
    @if $avatar-outline-width {
      border-width: $avatar-outline-width;
    }

    .vu-user-picture__image {
      width: $avatar-diameter - $avatar-gutter-width * 2 - $avatar-outline-width * 2;
      height: $avatar-diameter - $avatar-gutter-width * 2 - $avatar-outline-width * 2;

      @if $avatar-gutter-width {
        position: absolute;
        top: $avatar-gutter-width;
        left: $avatar-gutter-width;
      }
    }
  }

  @mixin gutter-color($color) {
    background: $color;
  }

  .vu-user-picture-wrap {
    height: inherit;
    width: inherit;
    position: relative;
  }

  .vu-user-picture__image {
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }

  .vu-user-picture__hover-mask {
    height: calc(100% - 1px);
    width: calc(100% - 1px);
    z-index: 2;
    position: absolute;
    top: 1px;
    background: transparent;
    border: 2px solid white;
    outline: 1px solid $blue-3;
  }

  .vu-user-picture--circle {
    &, .vu-user-picture-wrap, .vu-user-picture__image, .vu-user-picture__hover-mask {
      border-radius: 50%;
    }
  }
  .vu-presence {
    border-radius: 50%;
  }

  .vu-user-picture {
    position: relative;

    .vu-presence {
      background: white;
    }

    &--bg-inherit {
      &, .vu-presence {
        background: inherit;
      }
    }
  }

  .vu-presence {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;

    .vu-presence__indicator {
      &--offline {
        background-image: url('../../../src/assets/fonts/status-empty.svg');
        fill: $gray-dv-2;
      }

      &--online {
        background-image: url('../../../src/assets/fonts/status-ok.svg');
        color: $green-1;
      }
      
      &--busy {
        background-image: url('../../../src/assets/fonts/status-noway.svg');
        color: $red-1;
      }
      
      &--away {
        background-image: url('../../../src/assets/fonts/status-clock.svg');
        color: $yellow-away;
      }
    }
  }


  .vu-user-picture--clickable {
    cursor: pointer;
  }

  /* Sizes */
  .vu-user-picture {
    &--gutter {
      @include gutter-color(#ffffff);
    }
 
    &--tiny {
      @include avatar-size(18px);
    }
     
    &--small {
      @include avatar-size(26px, 12px, 8px);
    }

    &--medium {
      @include avatar-size(38px, 14px, 10px);

      &.vu-user-picture--gutter {      
        @include avatar-size-with-gutter(38px, 2px, 2px);
      }
    }

    &--medium-1 {
      @include avatar-size(44px, 16px, 12px);

      &.vu-user-picture--gutter {      
        @include avatar-size-with-gutter(38px, 2px, 2px);
      }
    }

    &--big {
      @include avatar-size(84px, 30px, 24px);
      &.vu-user-picture--gutter {
        @include avatar-size-with-gutter(84px, 5px, 0);
      }
    }
    
    &--bigger {
      @include avatar-size(104px, 32px, 28px);
      &.vu-user-picture--gutter {
        @include avatar-size-with-gutter(104px, 5px, 0);
      }
    }

    &--large {
      @include avatar-size(120px, 34px, 28px);
    }

    &--extra-large {
      @include avatar-size(154px, 44px, 36px);
    }
  }
</style>
