/* eslint-disable no-unused-vars */
import clickOutside from './v-click-outside';
import tooltip from './tooltip';
import mask from './mask';

import { denseGroup, denseClass } from './dense';

const plugin = {
  install(app, options = { disableTooltipsOnDevices: true }) {
    app.directive('click-outside', clickOutside);
    app.directive('mask', mask);
    tooltip.setConfig(options);
    app.directive('tooltip', tooltip);
    // Vue.directive('dense-class', denseClass);
    // if (config.dense) {
    //   Vue.directive('dense', denseGroup);
    // }
    // app.config.vuDisableTooltipsOnDevices = config.disableTooltipsOnDevices;
  },
  clickOutside,
  tooltip,
  mask,
  // denseGroup,
  // denseClass,
};

export default plugin;
