<template>
  <div class="vu-media-upload-droppable" :class="classes" @drop.prevent.stop="(e) => $emit('drop', e)">
    <slot name="drop-main">
      <div class="vu-media-upload-droppable__icon">
        <vu-icon icon="up" color="none"></vu-icon>
      </div>
    </slot>
    <slot name="drop-alt">
      <span class="vu-media-upload-droppable__label">{{ vuMediaUploadDropText }}</span>
    </slot>
  </div>
</template>
<script>
export default {
  name: 'vu-media-upload-droppable',
  props: {
    isOver: {
      type: Boolean,
    },
    validDrop: {
      type: Boolean,
    },
    // TD orange state.
  },
  emits: ['drop'],
  inject: {
    vuMediaUploadDropText: {
      default: 'Drop your files to upload',
    },
  },
  computed: {
    classes() {
      return {
        'vu-media-upload-droppable--valid': this.validDrop,
      };
    },
  },
  // created: {},
  mounted() {},
  beforeUnmount() {},
  methods: {},
};
</script>
