<template>
  <div
    class="vu-lightbox-bar"
    :class="{
      'lightbox-bar--responsive': responsive,
      'lightbox-bar--widget-header': widget,
    }"
  >
    <div class="lightbox-bar__left">
      <div
        v-if="showCompass && !widget"
        :class="['lightbox-bar__compass', { 'lightbox-bar__compass--disabled': disableCompass }]"
        @click="$emit('click-compass')"
      >
        <div class="lightbox-bar__compass-active" />
      </div>
      <slot name="lightbox-bar__object-type">
        <div class="lightbox-bar-menu-item lightbox-bar-menu-item--no-cursor">
          <div class="lightbox-bar__media-type" :style="{ 'background-color': type['backgroundColor'] }"
            @dragstart="(e) => $emit('media-type-drag-start', e)"
            @drag="(e) => $emit('media-type-drag', e)"
            @dragend="(e) => $emit('media-type-drag-end', e)"
            :draggable="hasDragEvent ? 'true' : 'false'">
            <span :class="`fonticon fonticon-${type['icon']}`"> </span>
          </div>
        </div>
      </slot>
      <div class="lightbox-bar__title">
        <slot name="lightbox-bar__title">
          <span>{{ label }}</span>
        </slot>
      </div>
    </div>
    <div class="lightbox-bar__right">
      <div class="lightbox-bar__menu">
        <template v-if="!responsive">
          <template v-for="(item, index) in _items" :key="`${uid}-${index}-rm`">
            <vu-dropdownmenu
              v-if="item.items && !item.hidden"
              :v-model="selectedItemsArray(_items)"
              :key="`lightbox-dropdownmenu_${uid}-${index}`"
              :items="item.items"
              :shift="true"
              :disabled="item.disabled"
              v-bind="{ overlay: dropdownOverlay }"
              class="lightbox-bar-dropdown-wrap"
              v-on="dropdownMenuListeners"
            >
              <template v-slot:default="{ active }">
                <vu-icon-btn
                  :icon="icon(item)"
                  :active="item.selected || active"
                  :disabled="item.disabled"
                  :color="!widget ? 'secondary' : 'default'"
                  class="lightbox-bar-menu-item"
                  v-tooltip.body.bottom="`${item.label || capitalize(item.name)}`"
                  @click="() => actionClick(item)"
                />
              </template>
            </vu-dropdownmenu>
            <vu-icon-btn
              v-else-if="!item.hidden"
              :icon="icon(item)"
              :active="item.selected"
              :disabled="item.disabled"
              :color="!widget ? 'secondary' : 'default'"
              class="lightbox-bar-menu-item"
              v-tooltip.body.bottom="`${item.label || capitalize(item.name)}`"
              @click="() => actionClick(item)"
            />
          </template>
        </template>
        <vu-dropdownmenu
          v-if="_dropdownMenuItems.length > 0"
          :v-model="selectedItemsArray(_dropdownMenuItems)"
          class="lightbox-bar-dropdown-wrap"
          :preventDropup="true"
          :items="_dropdownMenuItems"
          :position="'bottom-left'"
          :shift="true"
          v-bind="{ overlay: dropdownOverlay }"
          v-on="dropdownMenuListeners"
        >
          <template v-slot:default="{ active }">
            <vu-icon-btn
              :icon="menuIcon"
              :active="active"
              :color="!widget ? 'secondary' : 'default'"
              class="lightbox-bar-menu-item"
              :class="!responsive ? 'chevron-menu-icon' : ''"
              v-tooltip.body.bottom="`${moreActionsLabel}`"
            />
          </template>
        </vu-dropdownmenu>
        <slot name="lightbox-bar__special-actions"></slot>
        <div v-if="hasLeftToDividerContent && hasRightToDividerContent" class="lightbox-bar__divider">
          <hr class="divider divider--vertical" />
        </div>
        <template v-for="(item, index) in rightItems">
          <vu-icon-btn
            :key="`${uid}-sa-${index}`"
            v-if="!item.hidden"
            class="lightbox-bar-menu-item"
            :color="!widget ? 'secondary' : 'default'"
            :icon="icon(item)"
            :active="item.selected"
            :disabled="item.disabled"
            v-tooltip.body.bottom="`${item.label || capitalize(item.name)}`"
            @click="actionClick(item, 'side-action')"
          />
        </template>
        <vu-icon-btn
          v-if="showCloseIcon"
          class="lightbox-bar-menu-item"
          :color="!widget ? 'secondary' : 'default'"
          :icon="'close'"
          v-tooltip.body.bottom="closeLabel"
          @click="$emit(`close`, false)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import capitalize from '../../utils/capitalize';
import { getListenersFromAttrs } from '../../utils/vue';
import { uuidv4 } from '../../utils/guid';

export default {
  name: 'vu-lightbox-bar',
  emits: ['close', 'click-comment', 'click-download', 'click-information', 'click-share', 'media-type-drag-start', 'media-type-drag', 'media-type-drag-end'],
  props: {
    showCloseIcon: { default: () => true },
    showCompass: { default: () => true },
    label: {
      type: String,
      default: () => '',
    },
    type: {
      type: Object,
      default: () => {},
    },
    items: {
      type: Array,
      default: () => [],
    },
    customItems: {
      type: Array,
      default: () => [],
    },
    subItems: {
      type: Array,
      default: () => [],
    },
    rightItems: {
      type: Array,
      default: () => [],
    },
    responsive: {
      type: Boolean,
      default: () => false,
    },
    widget: {
      type: Boolean,
      default: () => false,
    },
    moreActionsLabel: {
      type: String,
      default: () => 'More',
    },
    disableCompass: {
      type: Boolean,
      required: true,
    },
    closeLabel: {
      type: String,
      default: () => 'Close',
    },
    dropdownOverlay: Boolean,
    onMediaTypeDragStart: Function, 
    onMediaTypeDrag: Function, 
    onMediaTypeDragEnd: Function,
  },
  data: () => ({
    getListenersFromAttrs,
    capitalize,
    uid: uuidv4(),
  }),
  computed: {
    menuIcon() {
      return this.responsive ? 'menu-dot' : 'chevron-down';
    },
    hasLeftToDividerContent() {
      /* eslint-disable operator-linebreak */
      return (
        (this.items.length > 0 && this.items.some((v) => !v.hidden)) ||
        this._dropdownMenuItems.length > 0 ||
        this.$slots['lightbox-bar__special-actions']
      );
      /* eslint-enable operator-linebreak */
    },
    hasRightToDividerContent() {
      return this.showCloseIcon || (this.rightItems && this.rightItems.length > 0 && this.rightItems.some((v) => !v.hidden));
    },
    hasDragEvent() {
      return this.onMediaTypeDragStart || this.onMediaTypeDrag || this.onMediaTypeDragEnd;
    },
    _items() {
      return this.actionsMergeSubs(this.items, this.customItems);
    },
    dropdownMenuListeners() {
      const listeners = this.getListenersFromAttrs(this.$attrs);
      if (listeners.close) {
        const object = { ...listeners };
        delete object.close;
        return object;
      }
      return listeners;
    },
    _dropdownMenuItems() {
      if (this.responsive) {
        const toOverflow = this._items.filter(({ nonResponsive, hidden }) => !nonResponsive && !hidden);
        if (this.subItems && this.subItems.length > 0) {
          toOverflow.push({
            name: 'more-actions',
            label: this.moreActionsLabel,
            items: this.subItems,
          });
        }
        return toOverflow;
      }
      return this.subItems;
    },
  },
  methods: {
    icon(item) {
      return item.icon ? `${item.icon}` : `${item.fonticon}`;
    },
    actionClick(item, type = 'primary-action') {
      if (!item.disabled) {
        if (item.handler) {
          item.handler(item);
        }
        this.$emit(`click-${item.name.toLowerCase()}`, item, { type });
      }
    },
    actionsMergeSubs(base, actions) {
      const common = actions.filter(({ name }) => base.find(({ name: baseName }) => name === baseName));
      const remaining = actions.filter(({ name }) => !common.find(({ name: baseName }) => name === baseName));
      base.forEach(({ name: elementName, items }) => {
        const action = common.find(({ name }) => name === elementName);
        if (action) {
          const { items: actionItems } = action;
          if (actionItems) {
            if (!Array.isArray(items)) {
              // eslint-disable-next-line no-param-reassign
              items = [];
            }
            items.push(...actionItems);
          }
        }
      });
      let array = [...base, ...remaining];
      array = array.map((a) => {
        if (a.text === undefined) {
          const text = this.capitalize(a.name);
          // eslint-disable-next-line no-param-reassign
          a.text = text;
        }
        return a;
      });
      return array;
    },
    selectedItemsArray(items) {
      if (this.customItems) {
        return this.getSelectedItems(items);
      }
      return [];
    },
    getSelectedItems(items) {
      let acc = [];
      // eslint-disable-next-line no-unused-expressions, operator-linebreak
      Array.isArray(items) &&
        items.forEach((item) => {
          if (item.items) {
            const children = this.getSelectedItems(item);
            acc = [acc, ...children];
          }
        });
      return acc.filter((i) => i.selected);
    },
  },
};
</script>
<style scoped lang="scss">
  @import '../../styles/_utils.scss';
  @import '../../styles/_variables.scss';
  @import './_variables.scss';

  .vu-lightbox-bar {
    display: flex;

    @include flex(0 0 $lightbox-bar-height);

    user-select: none;
    height: $lightbox-bar-height;
    line-height: $lightbox-bar-height;
    font-family: '3ds';
    font-size: 16px;
    font-weight: lighter;
    white-space: nowrap;
    min-width: 320px;
    flex-direction: row;
    z-index: 1;

    background-color: $main-background;
    color: $main-color;

    &.lightbox-bar--widget-header {
      background-color: $secondary-background;
      color: $secondary-color;

      .lightbox-bar__media-type {
        color: $secondary-media-type-color;
      }

      border-bottom: 1px solid $grey-4;
    }
  }

  .lightbox-bar__left {
    display: flex;

    @include flex(1 1 auto);

    overflow: hidden;
    text-align: left;
    height: 100%;
    position: relative;
    z-index: 2;

    .lightbox-bar__compass {
      @include flex(0 0 43px);

      position: relative;
      background-image: url(../../assets/small-compass.png);
      overflow: hidden;
      display: block;

      &:not(.lightbox-bar__compass--disabled):hover,
      .lightbox-bar--compass-open & {
        .lightbox-bar__compass-active {
          cursor: pointer;
          opacity: 1;
        }
      }

      .lightbox-bar__compass-active {
        background-image: url(../../assets/small-compass-active.png);
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: opacity 0.3s;
      }
    }

    .lightbox-bar__media-type {
      width: 36px;
      height: 36px;
      border: 1px solid $blue-1;
      font-size: $item-font-size;
      border-radius: 4px;
      margin: 4px;
      line-height: 36px;

      &[draggable='true'] {
        cursor: grab;
      }
    }

    .lightbox-bar__title {
      max-width: 100%;

      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;

      span {
        padding-left: 8px;
      }
    }

    @media screen and (min-width: $break-small) {
      z-index: 0;
      padding-right: 10px;
    }

    @media screen and (min-width: $break-medium) {
      padding-right: 10px;
    }
  }

  .lightbox-bar__right {
    display: flex;
    align-self: flex-end;

    @include flex(0 1 auto);

    z-index: 3;

    .lightbox-bar__menu {
      display: flex;

      @include flex(1 1 auto);

      .lightbox-bar-menu-item {
        &:hover,
        &.active,
        &.active:hover {
          color: $main-icon-hover;
        }
      }
    }
  }

  .lightbox-bar-menu-item {
    &,
    &.vu-icon-btn,
    & .vu-icon-btn {
      width: 44px;
      height: 44px;
      line-height: 44px;
    }
  }

  .lightbox-bar-menu-item {
    cursor: pointer;
    margin: 0;
    font-size: $item-font-size;
    position: relative;
    display: inline-block;
    line-height: $lightbox-bar-height;
    text-align: center;

    &--no-cursor {
      cursor: initial;
    }
  }

  .lightbox-bar__divider {
    padding: 4px;
    height: $lightbox-bar-height;
  }

  .divider {
    display: block;
    @include flex(0 0 auto);

    max-width: 100%;
    height: 0px;
    max-height: 0px;
    margin-top: 0;
    margin-bottom: 0;
    border: solid;
    border-width: thin 0 0 0;
    transition: inherit;

    &--vertical {
      align-self: stretch;
      border: solid;
      border-width: 0 thin 0 0;
      display: inline-flex;
      height: inherit;
      min-height: 100%;
      max-height: 100%;
      max-width: 0px;
      width: 0px;
      vertical-align: text-bottom;
    }
  }

  .tooltip {
    font-family: Arial, Helvetica, sans-serif;
  }
</style>
<style lang="scss">
  @import './_variables.scss';

  /* Resets because tooltip / dropdown are not detached  'injected' in UWA terminology */
  .vu-lightbox-bar {
    .tooltip-body {
      font-family: Arial, Helvetica, sans-serif;
    }
  }

  .lightbox-bar-dropdown-wrap > .vu-dropdown-menu__activator {
    height: $lightbox-bar-height;
  }
  /* End resets */
</style>
