<template>
  <div class="vu-tree-view">
    <vu-scroller>
      <vu-tree-view-item
        v-for="item in items"
        :key="`${item.id}`"
        :item="item"
        :loading="loading || innerLoading"
        :expanded="expanded"
        :selected="selected"
        :main="firstLevelBigger"
        @expand="toggleExpand"
        @select="toggleSelect"
        @load-complete="onLoad"
      >
      </vu-tree-view-item>
    </vu-scroller>
  </div>
</template>

<script>
export default {
  name: 'vu-tree-view',
  emits: ['update:selected', 'update:expanded', 'fetch', 'item-click'],
  props: {
    selected: {
      type: Array,
      default: () => [],
    },
    expanded: {
      type: Array,
      default: () => [],
    },
    loading: {
      type: Array,
      required: false,
    },
    items: {
      type: Array,
      required: true,
    },
    exclusive: {
      type: Boolean,
      default: true,
    },
    firstLevelBigger: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    innerLoading: [],
  }),
  methods: {
    toggleSelect(item) {
      if (this.selected.includes(item)) {
        const values = this.expanded.slice();
        values.splice(values.indexOf(item), 1);
        this.$emit('update:selected', values);
      } else if (this.exclusive) {
        this.$emit('update:selected', [item]);
      } else {
        this.$emit('update:selected', [item].concat(this.expanded || []));
      }
    },
    toggleExpand(item) {
      const values = this.expanded.slice();
      if (this.expanded.includes(item)) {
        values.splice(values.indexOf(item), 1);
        this.$emit('update:expanded', values);
      } else if (item.items === undefined) {
        this.$emit('fetch', item);
        if (this.loading === undefined) {
          this.innerLoading.push(item);
        } else {
          this.$emit('update:loading', [item].concat(this.loading || []));
        }
      } else {
        values.push(item);
        this.$emit('update:expanded', values);
      }
    },
    onLoad(item) {
      if (this.loading === undefined) {
        this.innerLoading.splice(this.innerLoading.indexOf(item));
      }
      if (item.items && item.items.length > 0 && !item.leaf) {
        this.$emit('update:expanded', [item].concat(this.expanded || []));
      }
    },
  },
};
</script>
<style lang="scss">
.vu-tree-view {
  flex-direction: column;

  &--main-item {
    height: 46px;
  }
}
</style>
