/* eslint-disable no-unused-vars */
/* eslint-disable no-param-reassign */
import { modal, ModalsContainer } from './components/vu-modal';
import { message, MessageContainer } from './components/vu-message';
import directive from './directives/index';

import './styles/index.scss';

// Auto imports all vue components
const req = require.context('./components', true, /.ue$/);
const components = req.keys()
  // except ModalsContainer
  .filter((key) => !key.includes('modal-container') && !key.includes('message-container'))
  .map(req);

// eslint-disable-next-line no-unused-vars
const install = (app, config = {}) => {
  const { lang, country } = config;

  components.forEach((el) => {
    app.component(el.default.name, el.default);
  });

  app.component('vu-modal-container', ModalsContainer); 
  app.component('vu-message-container', MessageContainer); 

  if (lang && country) {
    app.provide('lang', `${lang}-${country}`);
  } else {
    app.provide('lang', 'en-US');
  }
  app.provide('vuCollectionActions', null);
  app.provide('vuCollectionLazyImages', true);
  app.provide('vuTileEmphasizeText', false);
  app.provide('vuDateFormatWeekday', true);
  app.provide('vuDateFormatShort', false);
  app.provide('vuTreeViewLazy', true);
  app.provide('vuTreeViewIcon', 'chevron');

  directive.install(app, config);
};

const plugin = { install };
export default install;
export { 
  install, plugin, modal, message,
};
