<template>
  <div class="vu-user-name"
    :class="['vu-user-name--default-color', 'vu-user-name--default-size', 
    { 'vu-user-name--with-avatar' : shift },
    { 'vu-user-name--clickable' : clickable }]">
    <slot><span class="content"
    @click="$emit('click')">{{ firstName + ' ' + _lastName }}</span></slot>
  </div>
</template>
<script>
export default {
  name: 'vu-user-name',
  props: {
    firstName: String,
    lastName: String,
    toUpper: {
      type: Boolean,
      required: false,
      default: true,
    },
    shift: Boolean,
    clickable: Boolean,
  },
  computed: {
    _lastName() {
      return this.toUpper ? this.lastName.toUpperCase() : this.lastName;
    },
  },
};
</script>
<style scoped lang="scss">
  @import '../../styles/_variables.scss';
  @import './_variables.scss';

  .vu-user-name {
    .content {
      vertical-align: middle;

      &:hover {
        text-decoration: underline;
      }
    }

    &--default-color {
      border-radius: 15px;
      line-height: 20px;
    }

    &--default-size {
      background: $grey-1;
      color: $text-blue;
    }

    &--with-avatar {
      margin-left: -15px;
      padding: 6px 10px 6px 25px;
    }

    &--clickable {
      & .content:hover {
        cursor: pointer;
      }
    }
  }
</style>
