<template>
  <div class="vu-single-checkbox" :class="topClasses">
    <div class="toggle" :class="internalClasses">
      <input
        class="vu-single-checkbox__input"
        :type="radio ? 'radio' : 'checkbox'"
        :checked="radio ? group === modelValue : modelValue"
        v-bind="$attrs"
        :name="radio ? group : undefined"
        :value="radio ? value : undefined"
        :id="$attrs[id] || `${uid}`"
        :disabled="disabled"
        @click="input"
      />
      <template v-if="!standalone">
        <label class="control-label vu-single-checkbox__label" :for="$attrs[id] || `${uid}`">
          <vu-icon v-if="icon" :icon="icon" /><span class="vu-single-checkbox__inner-span">{{ label }}</span>
        </label>
        <slot name="label-prepend"></slot>
      </template>
    </div>
    <template v-if="hasExtraContent">
      <div class="vu-single-checkbox__extra-content">
        <slot> </slot>
      </div>
    </template>
  </div>
</template>

<script>
import disablable from '../mixins/disablable';
import validatable from '../mixins/validatable';
import { RegistrableInput } from '../mixins/registrable';
import { uuidv4 } from '../utils/guid';

export default {
  name: 'vu-single-checkbox',
  mixins: [validatable, RegistrableInput, disablable],
  inheritAttrs: false,
  props: {
    // String for Radio, Boolean for Switch/Default
    modelValue: {
      type: [String, Boolean],
      default: () => '',
    },
    label: {
      type: String,
      default: '',
    },
    // Removes slot and label
    standalone: {
      type: Boolean,
      default: false,
    },
    // Optional
    icon: {
      type: String,
      required: false,
    },
    // Exclusive with Switch
    radio: {
      type: Boolean,
      required: false,
    },
    // Required by radio.
    group: {
      type: String,
      required: false,
    },
    // Required by radio
    value: {
      type: String,
      required: false,
    },
    // Excludes radio
    switch: {
      type: Boolean,
      required: false,
    },
    id: {
      type: [String, Number],
      required: false,
    },
  },
  data: () => ({ uid: uuidv4() }),
  computed: {
    topClasses() {
      return {
        'vu-single-checkbox--switch': this.switch,
        'vu-single-checkbox--standalone': this.standalone,
        'vu-single-checkbox--checkbox': !this.switch && !this.radio,
        'vu-single-checkbox--radio': this.radio,
        'vu-single-checkbox--extra-content': this.hasExtraContent,
      };
    },
    internalClasses() {
      return {
        'toggle-icon': this.icon,
        'toggle-switch': this.switch,
        'toggle-primary': !this.switch,
      };
    },
    hasExtraContent() {
      return this.$slots.default && !this.standalone;
    },
  },
  methods: {
    input(e) {
      if (!this.radio) {
        return this.$emit('update:modelValue', e.target.checked);
      }
      return this.$emit('update:modelValue', e.target.value);
    },
  },
};
</script>

<style scoped lang="scss">
.toggle > .vu-icon {
  position: relative;
  cursor: pointer;
}
.toggle > .control-label {
  user-select: none;
}

.label--bold {
  font-weight: 700;
}

// Custo for Swymv3 Spec - Potential UX mainstream.
.vu-single-checkbox--extra-content.vu-single-checkbox--radio {
  --vu-sch-ec-marg-left: 66px;

  .toggle {
    margin-bottom: 0;
  }

  .toggle-primary [type='radio']:not(:checked) + .control-label,
  .toggle-primary [type='radio']:checked + .control-label {
    padding-left: 20px;
  }

  .toggle-primary [type='radio']:not(:checked) + .control-label:before,
  .toggle-primary [type='radio']:checked + .control-label:before {
    top: 2px;
  }

  .toggle-primary [type='radio']:not(:checked) + .control-label:after,
  .toggle-primary [type='radio']:checked + .control-label::after {
    top: 7px;
  }

  .control-label {
    .vu-icon {
      font-size: 18px;
    }
    .vu-single-checkbox__inner-span {
      font-weight: 700;
    }
    :deep(.vu-icon.fonticon) {
      margin: 0;
      margin-right: 8px;
    }
  }
}
</style>
<style>
.vu-single-checkbox {
  --vu-sch-ec-marg-left: 20px;
}

.vu-single-checkbox__extra-content {
  margin-left: var(--vu-sch-ec-marg-left);
}
</style>
