<template>
  <div class="vu-progress-circular">
    <div
      :class="['vu-progress-circular__circle', !hexColor ? `vu-progress-circular--${color}` : '']"
      :style="{
        background: `conic-gradient( currentcolor ${progressAngle}deg, ${unfilledColor} ${progressAngle}deg)`,
        width: radiusPx,
        height: radiusPx,
        color: hexColor !== undefined && hexColor,
        '-webkit-mask': `radial-gradient(${radius * (2 / 5)}px, #0000 98%, #000)`,
      }"
    >
      <template v-if="renderHatch">
        <div class="vu-progress-circular__hatch-container" :class="{ 'vu-progress-circular__hatch-clip': progressAngle < 180 }">
          <div class="vu-progress-circular__hatch" :style="`transform: rotate(${progressAngle}deg)`"></div>
        </div>
      </template>
    </div>
    <div class="vu-progress-circular__content">
      <template v-if="completedView && this.$slots['complete']">
        <slot name="complete"></slot>
      </template>
      <template v-else>
        <slot name="default">
          <Transition name="fade" mode="out-in">
            <div key="uncomplete-view" :style="{ fontSize: `${radius / 5}px` }">{{ Math.round((progressAngle / 360) * 100) }}%</div>
          </Transition>
        </slot>
      </template>
    </div>
  </div>
</template>
<script>
import colorable from '../mixins/colorable';

export default {
  name: 'vu-progress-circular',
  mixins: [colorable],
  data() {
    return {
      progressAngle: (this.value / this.total) * 100 * 3.6,
      intervalId: null,
      completedView: this.value >= this.total,
    };
  },
  props: {
    value: {
      default: 0,
      type: Number,
    },
    total: {
      default: 100,
      type: Number,
    },
    radius: {
      default: 60,
      type: Number,
    },
    noHatch: {
      default: false,
      type: Boolean,
    },
    unfilledColor: {
      type: String,
      default: '#d1d4d4', // $grey-4
    },
    color: {
      type: String,
      default: () => 'default',
      validator(val) {
        return ['default', 'success', 'warning', 'error'].includes(val);
      },
    },
    hexColor: {
      type: String,
      required: false,
    },
    speedModifier: {
      type: Number,
      default: 1,
    },
  },
  watch: {
    total() {
      this.animateProgress();
    },
    value() {
      this.animateProgress();
    },
  },
  computed: {
    radiusPx() {
      return `${this.radius}px`;
    },
    formattedCompletedCount() {
      return this.value < this.total ? this.value : this.total;
    },
    progressPercentage() {
      return (this.value / this.total) * 100;
    },
    renderHatch() {
      return !this.noHatch && this.value < this.total;
    },
  },
  methods: {
    updateAngle(finalAngle) {
      this.completedView = false;
      const difference = Math.abs(this.progressAngle - finalAngle);
      if (Math.round(this.progressAngle) < Math.round(finalAngle)) {
        if (difference <= this.speedModifier) {
          this.progressAngle = finalAngle;
        } else {
          this.progressAngle += this.speedModifier;
        }
      } else if (Math.round(this.progressAngle) > Math.round(finalAngle)) {
        if (difference <= this.speedModifier) {
          this.progressAngle = finalAngle;
        } else {
          this.progressAngle -= this.speedModifier;
        }
      } else {
        clearInterval(this.intervalId);
        if (this.value >= this.total) {
          this.completedView = true;
        }
      }
    },
    animateProgress() {
      if (this.intervalId) {
        clearInterval(this.intervalId);
      }
      const finalAngle = this.progressPercentage * 3.6;
      this.intervalId = setInterval(this.updateAngle.bind(this, finalAngle), 5);
    },
  },
  beforeUnmount() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  },
};
</script>

