      background: linear-gradient(to right, $cyan-1 0, $cyan-1 5px, rgba($cyan-1, 0.7) 50%),
        repeating-linear-gradient(to left, $cyan-1 0, $cyan-1 4px, white 4px, white 10px);
    }
  }

  &--success {
    color: $green-1;

    .vu-progress-circular__hatch {
      background: linear-gradient(to right, $green-1 0, $green-1 5px, rgba($green-1, 0.7) 50%),
        repeating-linear-gradient(to left, $green-1 0, $green-1 4px, white 4px, white 10px);
    }
  }
  &--warning {
    color: $orange-1;

    .vu-progress-circular__hatch {
      background: linear-gradient(to right, $orange-1, $orange-1 5px, rgba($orange-1, 0.7) 50%),
