// eslint-disable-next-line import/no-extraneous-dependencies
import { shallowReactive, reactive } from 'vue';
import uuid from '../../utils/uuid';
import Modal from './vu-modal.vue';
import ModalsContainer from './vu-modal-container.vue';

export function defineApi() {
  let _modalComponent = null;

  return {
    show(args) {
      this.hide();
      return new Promise((resolve, reject) => {
        const defaultModal = {
          value: true,
          id: uuid(),
          component: _modalComponent,
          bind: reactive({ ...args, show: true }),
          on: {
            close: () => { this.hide(defaultModal); reject(); },
            confirm: (val) => { this.hide(defaultModal); resolve(val); },
            cancel: () => { this.hide(defaultModal); reject(); },
          },
        };
        this._modals.push(shallowReactive(defaultModal));
      });
    },
    hide(instance) {
      if (instance) {
        const modal = this._modals.find((el) => el.id === instance.id);
        modal.bind.show = false;
        setTimeout(() => { // in case the hide is animated, give it time to end the animation before destroying the component
          const index = this._modals.findIndex((el) => el.id === instance.id);
          if (index > -1) { this._modals.splice(index, 1); }
        }, 1000);
      } else {
        // eslint-disable-next-line no-param-reassign
        this._modals.forEach((modal) => { modal._cancel = true; });
        this._modals.splice(0, this._modals.length);
      }
    },
    alert(options) {
      return this.show(options);
    },
    confirm(options) {
      return this.show({
        showCancelIcon: true,
        showCancelButton: true,  
        ...options,
      });
    },  
    prompt(options) {
      return this.show({
        showCancelIcon: true,
        showCancelButton: true,
        showInput: true,
        ...options,
      });
    },
    _modals: shallowReactive([]),
    _setDefaultModal(modalComponent) {
      _modalComponent = modalComponent;
    },
  };
}

function bindApi(component, api) {
  const _component = { ...component, props: { ...component.props } };
  Object.assign(_component.props, {
    api: { type: Object, default: () => api },
  });
  return _component;
}

export function defineModal(api) {
  const modalComponent = bindApi(Modal, api);
  api._setDefaultModal(modalComponent);
  return modalComponent;
}

export function defineContainer(api) {
  return bindApi(ModalsContainer, api);
}
