<template>
  <vu-popover
    :side="side"
    :show="show"
    arrow
    shift
    :positions="['bottom', 'top']"
    :attach="'body'"
    :contentClass="'vu-rich-user-tooltip'"
    :activator="activator"
  >
    <template v-slot:default>
      <slot name="default">
        <vu-user-picture
          :id="user.id"
          clickable
          v-tooltip.top="getFullname(user)"
          :src="user.imgSrc"
          :presence="user.presence"
          class="rich-user-tooltip__default-content"
        />
      </slot>
    </template>
    <template v-slot:arrow="{ side, shift }">
      <div
        v-show="!shift"
        class="rich-user-tooltip__arrow popover-arrow"
        :class="`rich-user-tooltip__arrow--${side}`"
      ></div>
    </template>
    <template v-slot:title="{ side }">
      <div class="rich-user-tooltip__header" :class="`rich-user-tooltip__header--${side}`">
        <div class="rich-user-tooltip__header__wrap-name">
          <label
            class="rich-user-tooltip__header__name"
            v-tooltip="getFullname(user)"
            @click="$emit('see-profile', user.id)"
            >{{ getFullname(user) }}
          </label>
        </div>
        <div class="rich-user-tooltip__header__topbar"></div>
        <div class="rich-user-tooltip__avatar-wrap"
          v-tooltip.bottom="getFullname(user)">
          <vu-user-picture
            class="rich-user-tooltip__header__avatar"
            size="big"
            :clickable="true"
            :id="user && user.id"
            :gutter="true"
            :presence="user.presence"
            @click="$emit('see-profile', user.id)"
          />
        </div>
      </div>
    </template>
    <template v-slot:body v-if="hasInfo || hasContacts || true">
      <div class="rich-user-tooltip__info" v-if="hasInfo">
        <label v-if="user.company" class="rich-user-tooltip__info__company">
          {{ user.company }}
        </label>
        <label v-if="countryImg || countryLabel" class="rich-user-tooltip__info__locale">
          <img v-if="countryImg" class="rich-user-tooltip__info__flag" :src="countryImg" />
          <span v-if="countryLabel" class="rich-user-tooltip__info__country"
            >{{ countryLabel }}
          </span>
        </label>
      </div>
      <slot name="content"></slot>
      <template v-if="hasContacts">
        <label class="rich-user-tooltip__contacts__label">
          {{ contactsLabelPart1
          }}<span
            class="rich-user-tooltip__contacts__amount"
            v-tooltip.bottom="vuUserLabels.profile"
            @click="$emit('see-profile', user.id)"
            >{{ contacts.length }}</span
          ><template v-if="contactsLabelPart2">{{ contactsLabelPart2 }}</template>
        </label>
        <div class="rich-user-tooltip__contacts__list">
          <template v-for="contact in visibleContacts" :key="contact.id || uuid()">
            <vu-user-picture
              :id="contact.id || uuid()"
              :clickable="true"
              v-tooltip.bottom="getFullname(contact)"
              @click="$emit('see-profile', contact.id)"
            />
          </template>
          <vu-user-picture
            v-if="overflowContact"
            class="rich-user-tooltip__overflow_contact"
            :style="{
              '--numberOfOverflowingContacts': numberOfOverflowingContactsCssVariable,
            }"
            :clickable="true"
            hoverable
            v-tooltip.bottom="vuUserLabels.profile"
            :id="overflowContact.id || uuid()"
            @click="$emit('see-profile', overflowContact.id)"
          />
        </div>
      </template>
      <div class="rich-user-tooltip__footer">
        <div class="rich-user-tooltip__footer__left">
          <slot name="footer-left">
            <vu-icon-btn
              v-if="!(hiddenActions.length && hiddenActions.includes('network'))"
              :icon="actions.network.icon"
              class="add-network"
              v-tooltip.bottom="vuUserLabels['network']"
              :disabled="disabledActions.length > 0 && disabledActions.includes('network')"
              @click="(e) => { if (!isDisabled('network')) { $emit('network', user.id); } }"
            />
          </slot>
        </div>
        <slot name="footer-right">
          <template v-for="action in RHSactions" :key="action.name">
            <vu-icon-btn
              class="right-btn"
              v-if="!(hiddenActions.length && hiddenActions.includes(action.name))"
              :icon="action.icon"
              v-tooltip.bottom="vuUserLabels[action.name]"
              :disabled="isDisabled(action.name)"
              @click="(e) => { if (!isDisabled(action.name)) { $emit(action.name, user.id); } }"
            />
          </template>
        </slot>
      </div>
    </template>
  </vu-popover>
</template>

<script>
import clickoutside from '../../directives/v-click-outside';
import userLabels from '../../assets/nls/userLabels';
import * as Flags from '../../assets/flags/flags';

// eslint-disable-next-line import/named
import { validate as personValidator, validateName, getFullname } from '../../model/person';
import { validate as userActionsValidator } from '../../model/userActionsValidator';

// eslint-disable-next-line import/named, no-unused-vars
import { actions, RHSactions } from '../../model/actions';
import uuid from '../../utils/uuid';

/* eslint-disable vue/no-reserved-keys */
export default {
  name: 'vu-rich-user-tooltip',
  emits: ['network', 'message', 'audio', 'conferencing', 'screenshare', 'see-profile'],
  directives: {
    'click-outside': clickoutside,
  },
  inject: {
    vuUserLabels: {
      default: () => userLabels,
    },
    vuDebug: {
      default: false,
    },
  },
  props: {
    show: {
      type: Boolean,
      required: false,
    },
    user: {
      type: Object,
      validator: personValidator,
      required: true,
    },
    disabledActions: {
      type: Array,
      validator: userActionsValidator,
      required: false,
      default: () => [],
    },
    hiddenActions: {
      type: Array,
      validator: userActionsValidator,
      required: false,
      default: () => [],
    },
    side: {
      type: String,
      default: 'bottom',
    },
    attach: {
      default: false,
    },
    activator: {
      type: Object,
      default: undefined,
    },
  },
  watch: {
    show(value) {
      this.innerShow = value;
    },
    contacts: {
      immediate: true,
      handler() {
        this.parseContactsInCommonLabel();
      },
    },
  },
  /* eslint-disable no-unused-vars */
  data: () => ({
    overflowHover: false,
    actions,
    RHSactions,
    uuid,
    getFullname,
    validateName,
    contactsLabelPart2: '',
    contactsLabelPart1: '',
    visibleAmount: 7,
  }),
  /* eslint-enable no-unused-vars */
  computed: {
    hasInfo() {
      return this.user.company || this.user.country;
    },
    hasContacts() {
      return Array.isArray(this.user.contacts) && this.user.contacts.length > 0;
    },
    contacts() {
      return this.hasContacts ? this.user.contacts : [];
    },
    countryImg() {
      return !this.user.countryCode || !Flags[this.user.countryCode.toUpperCase()]
        ? false
        : Flags[this.user.countryCode.toUpperCase()];
    },
    countryLabel() {
      return this.user.countryCode && this.vuUserLabels[this.user.countryCode];
    },
    overflows() {
      return this.user.contacts && this.user.contacts.length > 7;
    },
    visibleContacts() {
      return this.hasContacts && this.overflows
        ? this.contacts.slice(0, this.visibleAmount)
        : this.contacts;
    },
    overflowContact() {
      return this.hasContacts && this.overflows
        ? this.contacts[this.visibleAmount]
        : null;
    },
    numberOfOverflowingContactsCssVariable() {
      return `"${this.contacts.length - this.visibleAmount}"`;
    },
  },
  methods: {
    parseContactsInCommonLabel() {
      // eslint-disable-next-line no-undef
      if (!this.vuUserLabels.contactsInCommon && this.vuDebug) {
        console.warn('contactsInCommon nls is missing');
        return;
      }
      let { contactsInCommon: str } = this.vuUserLabels;
      const hasPlural = str.match(/\$\(.*\)/).length > 0;
      if (this.contacts.length > 1 && hasPlural) {
        str = str.replace('$(', '').replace(')', '');
      } else {
        str = str.replace(/\$\(.*\)/, '');
      }
      str = str.split('###');
      // eslint-disable-next-line prefer-destructuring
      this.contactsLabelPart1 = str[0];
      // eslint-disable-next-line prefer-destructuring
      this.contactsLabelPart2 = str[1];
    },
    isDisabled(value) {
      return this.disabledActions.length > 0 && this.disabledActions.includes(value);
    },
  },
};
</script>
<style scoped lang="scss">

@import './variables.scss';
@import '../../styles/variables.scss';
@import '../../styles/avatar.scss';

$name: 'rich-user-tooltip';

$popover-bottom-margin-top: 10px;

$avatar-diameter: $avatar-big-diameter;

$label-font-size: 15px;
$avatar-margin: 10px;
$text-margin-left: $avatar-diameter + $avatar-margin;
$text-spacing: 10px;
$topbar-height: 44px;
$first-content-margin: $avatar-diameter + 10px - $topbar-height;

$contacts-list-h-margin: 10px;
$contacts-list-v-margin: 0px;
$contacts-list-h-padding: 8px;
$contacts-list-v-padding: 8px;
$contacts-list-bg-color: $grey-2;
$contacts-list-border-radius: 28px;
$contacts-list-space-between-items: 3px;

.vu-#{$name} {

  &.popover.bottom {
    border-top: 0;

    .#{$name}__arrow--bottom {
      border-bottom-color: transparent;

      &::after {
        top: 2px;
        border-bottom-color: $blue-4;
      }
    }
    .#{$name}__arrow--top {
      bottom: -9px;
    }
  }

  & .#{$name} {
    &__header {
      padding: 0;
      height: $topbar-height;
      position: relative;
    }

    &__header__wrap-name {
      width: calc(100% - $text-margin-left - 2 * $avatar-margin);
      top: 16px;
      left: $text-margin-left + $avatar-margin;
      
      z-index: 1;
      position: absolute;

      .#{$name}__header__name {
        font-size: $label-font-size;
        font-weight: bold;
        color: white;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        cursor: pointer;
      
        &:hover {
          text-decoration: underline;
        }   
      }
    }

    &__avatar-wrap {
      position: absolute;
      left: $avatar-margin;
      top: $avatar-margin;
    }

    &__header__topbar {
      position: absolute;
      top: 0;
      width: 100%;
      height: $topbar-height;

      display: flex;
      flex-direction: row-reverse;
      font-size: 12px;
    }

    &__info {
      margin-left: $text-margin-left;
      min-height: $avatar-diameter + 10px - $topbar-height;
    }

    &__info {
      label {
        width: 100%;
        margin: 0;
      }

      &__locale {
        font-weight: normal;
      }

      &__company {
        font-size: 13px;
      }
      &__company:last-child {
        margin-bottom: 23px;
      }

      &__flag {
        display: inline;
        width: auto;
        height: 13px;
        margin: 0 0.4em 0 0;
      }

      &__country {
        font-size: 13px;
        vertical-align: bottom;
      }
    }

    &__contacts__label {
      font-size: 12px;
      color: $grey-6;
      margin: 4px 10px 4px 10px;

      &:first-child {
        margin-top: $first-content-margin + 4px;
      }
    }

    &__contacts__list {
      display: flex;
      flex-direction: row;
      margin: 0;
      background-color: $contacts-list-bg-color;
      border-radius: $contacts-list-border-radius;
      padding: $contacts-list-v-padding $contacts-list-h-padding;

      .vu-user-picture {
        flex: 0 0 auto;

        &:last-child {
          margin-right: 0;
        }
      }

      .#{$name}__overflow_contact {
        --numberOfOverflowingContacts: '';
        position: relative;

        // Number
        &::before {
          content: var(--numberOfOverflowingContacts);
          position: absolute;
          height: 100%;
          width: 100%;
          line-height: 36px;
          text-align: center;
          font-size: 14px;
          font-weight: bold;
          color: white;
          z-index: 3;
          margin-top: 1.5px;
        }
      }
    }

    &__footer {
      display: flex;
      position: relative;
      justify-content: flex-end;
      margin: 6px 10px 0 10px;

      &:first-child {
        margin-top: $first-content-margin + 6px;
      }

      &__left {
        flex: 1 1 0;
        align-items: left;
      }

      :deep(.vu-icon) {
        font-size: 22px;
      }
      
      & {
        :deep(.vu-icon-btn.right-btn) {
          margin-left: 3px;
        }
      }
    }
  }
}

.#{$name}__contacts__list {
  width: auto;
  margin: 0 $contacts-list-h-margin;
  gap: 3px;
}

.#{$name}__contacts__amount {
  color: $blue-3;

  &:hover {
    text-decoration: underline;
    cursor: pointer;
  }
}
</style>
<style lang="scss">

@import './_variables.scss';
@import '../../styles/variables.scss';
@import '../../styles/avatar.scss';

$name: 'rich-user-tooltip';

$border-radius: 4px;

.vu-#{$name} {
  &.popover.popover-root {
    width: 366px;
  }

  .#{$name}__header,
  .#{$name}__header__topbar {
    border-radius: $border-radius $border-radius 0 0;
  }

  .#{$name} {
    // &__header--bottom {
    * &__header {
      .#{$name}__header__topbar {
        background-color: $blue-4;
        border-top: 0;
      }
    }
  }

  .popover-title {
    background-color: white;
  }

  .popover-body {
    margin-top: -1px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    padding: 6px 10px 10px 10px
  }

  .#{$name}__overflow_contact {
    .vu-user-picture__image {
      // Layer
      &::before {
        content: '';
        position: absolute;
        height: calc(100%);
        width: calc(100%);
        border-radius: 50%;
        background: #000000;
        opacity: 0.5;
        z-index: 2;
      }
    }
  }

}

</style>
