<template>
  <div>
    <slot name="lightbox-activator" />
    <div
      ref="lightbox"
      class="vu-lightbox"
      :class="{
        'lightbox--responsive': transforms.responsive,
        'lightbox--widget-header': widget,
        'vu-lightbox--appear-faster': !widget && !noAnimation && fasterAnimation,
        'vu-lightbox--appear-fast': !widget && !noAnimation && !fasterAnimation,
      }"
      :style="{
        'zIndex': zIndex,
      }"
      :data-id="uid"
    >
      <vu-lightbox-bar
        :label="title"
        :showCompass="!noCompass"
        :class="{ 'lightbox-bar--compass-open': openCompass }"
        :type="typeInfo"
        :items="_primaryActions"
        :subItems="menuActions"
        :rightItems="_sideActions"
        :responsive="transforms.responsive"
        v-on="{ ...listeners, ...listenersFromProps }"
        v-bind="{ disableCompass, customItems, dropdownOverlay, widget, moreActionsLabel, closeLabel }"
        @click-compass="
          () => {
            if (!disableCompass) {
              openCompass = !openCompass;
              compassAlreadyOpened = true;
            }
            $emit('click-compass', openCompass);
          }
        "
      >
        <template v-slot:lightbox-bar__object-type="data">
          <slot name="lightbox-bar__object-type" v-bind="data" />
        </template>
        <template v-slot:lightbox-bar__title="data">
          <slot name="lightbox-bar__title" v-bind="data" />
        </template>
        <template #lightbox-bar__special-actions>
          <slot name="lightbox-bar__special-actions" />
        </template>
      </vu-lightbox-bar>
      <div class="lightbox__overlay" />
      <div class="lightbox__content" ref="content" :style="transforms['center'] || {}">
        <slot name="lightbox-content" />
      </div>
      <div
        v-if="!noCompass && compassAlreadyOpened"
        v-show="openCompass"
        class="vu-panel lightbox__panel lightbox__panel--left column"
        :style="transforms['left'] || {}"
      >
        <iframe class="compass" :src="compassIframeUrl" />
        <vu-icon-btn
          v-if="transforms.responsive"
          icon="close"
          style="position: absolute; right: 0; top: 0; zindex: 21"
          @click="openCompass = false"
        />
      </div>
      <template v-for="({ name, show, showClose = false, showEdit, classes = [], title }, index) in _panels" :key="`${uid}-${index}`">
        <div
          class="vu-panel lightbox__panel column"
          :class="[...classes, 'lightbox__panel--right', { 'panel--responsive': transforms.responsive }]"
          v-show="show"
          :style="transforms['right']"
        >
          <div v-if="title" class="panel__header">
            <span class="panel__title">
              <span class="panel__title__text">{{ title }}</span>
              <vu-icon-btn v-if="showEdit" class="panel__edit__icon" icon="pencil" @click="$emit(`panel-edit-${name}`)" />
            </span>
            <vu-icon-btn v-if="showClose" class="panel__close_icon" icon="close" @click="$emit(`close-panel-${name}`)" />
          </div>
          <vu-icon-btn v-else-if="transforms.responsive || showClose" class="panel__close_icon" icon="close" @click="$emit(`close-panel-${name}`)" />
          <div :class="`vu-dynamic-panel-wrap-${name}`" class="panel__content">
            <slot :name="`lightbox-panel-${name}`" />
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import types from './types';

import primaryActions from './primaryActions';
import capitalize from '../../utils/capitalize';
import sideActions from './sideActions';
import { getListenersFromAttrs } from '../../utils/vue';
import { uuidv4 } from '../../utils/guid';

export default {
  name: 'vu-lightbox',
  data() {
    return {
      panelStates: [],
      openCompass: false,
      compassAlreadyOpened: false,
      compassPath: 'webapps/i3DXCompassStandalone/i3DXCompassStandalone.html',
      resizeObserver: {},
      transforms: {
        responsive: false,
        left: {},
        center: {},
        right: {},
      },
      capitalize,
      customItems: [],
      getListenersFromAttrs,
      uid: uuidv4(),
    };
  },
  emits: ['close', 'click-comment', 'click-information', 'click-share', 'click-download', 'media-type-drag-start', 'media-type-drag', 'media-type-drag-end'],
  props: {
    title: {
      type: String,
      default: () => '',
    },
    userId: {
      type: String,
      required: false,
    },
    panels: {
      type: Array,
      required: false,
      default: () => [{}],
    },
    widget: {
      type: Boolean,
      default: () => false,
    },
    objectType: {
      type: [String, Object],
      default: () => 'picture',
      validator: (val) => !!types[val] || (val && val.icon && val.backgroundColor),
    },
    primaryActions: {
      type: [Array, String],
      default: () => primaryActions,
    },
    customActions: {
      type: Boolean,
      default: () => false,
    },
    menuActions: {
      type: Array,
      required: false,
      default: () => [],
    },
    sideActions: {
      type: Array,
      default: () => sideActions,
    },
    customSideActions: {
      type: Boolean,
      default: () => false,
    },
    noObjectType: {
      type: Boolean,
      default: () => false,
    },
    disableCompass: {
      type: Boolean,
      default: () => false,
    },
    zIndex: {
      type: Number,
      default: () => 100,
    },
    moreActionsLabel: {
      type: String,
      default: () => 'More',
    },
    closeLabel: {
      type: String,
      default: () => 'Close',
    },
    noAnimation: {
      type: Boolean,
      default: () => false,
    },
    fasterAnimation: {
      type: Boolean,
      default: () => false,
    },
    hideCloseIcon: {
      type: Boolean,
      default: () => false,
    },
    dropdownOverlay: Boolean,
    onClose: Function,
    'onClick-comment': Function,
    'onClick-download': Function,
    'onClick-information': Function,
    'onClick-share': Function,
    'onMedia-type-drag-start': Function,
    'onMedia-type-drag': Function,
    'onMedia-type-drag-end': Function,
  },
  created() {
    if (!this.panels.find(({ show }) => show !== undefined)) {
      this.panelStates = this.panels.map((val) => ({ ...val, show: false }));
    }
  },
  computed: {
    typeInfo() {
      return typeof this.objectType === 'object' ? this.objectType : types[this.objectType];
    },
    compassIframeUrl() {
      return `${this.serviceUrl || ''}/${this.compassPath}${this.userId ? `#userId:${this.userId}` : ''}`;
    },
    listeners() {
      return getListenersFromAttrs(this.$attrs, true);
    },
    listenersFromProps() {
      return this.getListenersFromAttrs(this.$props, true);
    },
    _panels() {
      return this.panelStates.length > 0 ? this.panelStates : this.panels;
    },
    showRightPanel() {
      return this._panels.find(({ show }) => show);
    },
    noCompass() {
      return this.widget;
    },
    _primaryActions() {
      const actions = this.primaryActions;
      const base = primaryActions;
      if (this.widget) {
        const infoIcon = actions.find(({ name }) => name === 'information');
        const commentAction = actions.find(({ name }) => name === 'comment');
        if (infoIcon && !infoIcon.fonticon) {
          base.find(({ name }) => name === 'information').fonticon = 'info';
        }
        if (commentAction && !commentAction.fonticon) {
          base.find(({ name }) => name === 'comment').fonticon = 'comment';
        }
      }
      return this.actionsMerge(actions, base, this.customActions);
    },
    _sideActions() {
      return this.actionsMerge(this.sideActions, sideActions, this.customSideActions);
    },
  },
  mounted() {
    this.onResize();
    const observer = new ResizeObserver(() => {
      this.onResize();
    });
    observer.observe(this.$refs.lightbox);

    this.resizeObserver = observer;

    const that = this;
    if (!this.noCompass && window && window.require) {
      window.require(['DS/UWPClientCode/Data/Utils', 'DS/UWPClientCode/PublicAPI'], (DataUtils, PublicAPI) => {
        this.getCompassUrl = () => {
          DataUtils.getServiceUrl({
            serviceName: '3DCompass',
            onComplete: (url) => {
              that.serviceUrl = url;
            },
            onFailure: () => {
              // eslint-disable-next-line no-undef
              if (UWA && UWA.debug) {
                // eslint-disable-next-line no-console
                console.error('Lightbox Compass failed to retrieve 3DCompass service url');
              }
            },
            scope: that,
          });
        };

        if (!this.userId) {
          PublicAPI.getCurrentUser().then(
            ({ login }) => {
              // eslint-disable-next-line vue/no-mutating-props
              that.userId = login;
              this.getCompassUrl();
            },
            // eslint-disable-next-line comma-dangle
            () => this.getCompassUrl()
          );
        } else {
          this.getCompassUrl();
        }
      });
    }
  },
  watch: {
    openCompass() {
      this.onResize();
    },
    showRightPanel() {
      this.onResize();
    },
  },
  methods: {
    addCustomAction(item) {
      const existing = this.customItems.find(({ name }) => name === item.name);
      if (existing) {
        this.customItems[this.customItems.indexOf(existing)] = item;
      } else {
        this.customItems.push(item);
      }
    },
    clearCustomActions() {
      this.customItems = [];
    },
    showPanel(name, forceHide = true) {
      if (!this.panelStates.length) return;
      if (forceHide) this.hideAllPanels(name);
      const panel = this.panelStates.find(({ name: panelName }) => name === panelName);
      panel.show = true;
    },
    hidePanel(name) {
      if (!this.panelStates.length) return;
      const panel = this.panelStates.find(({ name: panelName }) => name === panelName);
      panel.show = false;
    },
    // eslint-disable-next-line no-unused-vars
    hideAllPanels(except = '') {
      if (!this.panelStates.length) return;
      this.panelStates
        .filter(({ name }) => name !== except)
        .forEach((panel) => {
          // eslint-disable-next-line no-param-reassign
          panel.show = false;
        });
    },
    actionsMerge(actions, base, customized) {
      let array = actions;
      if (!customized) {
        array = actions.slice(0, base.length).filter(({ name }) => base.find(({ name: baseName }) => name === baseName));
        // put in order !
        array = array.map((val) => ({
          // If component user messes up order \o/
          ...base.find(({ name }) => val.name === name),
          ...val,
        }));
      }
      array = array.map((a) => {
        if (a.text === undefined) {
          const text = this.capitalize(a.name);
          // eslint-disable-next-line no-param-reassign
          a.text = text;
        }
        return a;
      });
      return array;
    },
    onResize() {
      const { clientWidth: width } = this.$refs.lightbox;
      let size;
      if (width > 639) {
        const panelSize = Math.min(width * 0.125 + 240, 480);
        size = {
          responsive: false,
          left: {
            width: `${panelSize}px`,
          },
          center: {
            'margin-left': this.openCompass ? `${panelSize}px` : 0,
            'margin-right': this.showRightPanel ? `${panelSize}px` : 0,
          },
          right: {
            width: `${panelSize}px`,
          },
        };
      } else {
        size = { responsive: true, center: {}, right: {} };
      }
      this.transforms = size;
    },
  },
  beforeUnmount() {
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
    delete this.resizeObserver;
  },
};
</script>
<style scoped lang="scss">
@import '../../styles/_utils.scss';
@import '../../styles/_variables.scss';
@import '_variables.scss';

.vu-lightbox {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.lightbox__overlay {
  position: absolute;
  top: $lightbox-bar-height;
  left: 0;
  height: calc(100% - #{$lightbox-bar-height});
  width: 100%;
  background-color: $grey-1;
}

.lightbox__content {
  @include flex(1 1 auto);
  flex-direction: row;
  position: relative;
  height: 100%;
  z-index: 1; // this is required for a canvas glitch with 3DSketch
}

.lightbox__panel {
  height: 100%;
  overflow: hidden;
  position: absolute;
  background: #fff;
  top: 0;
  display: flex;
  flex-direction: column;

  z-index: 20;

  &--left {
    left: 0;
    border-right: $grey-3 1px solid;
  }

  &--right {
    right: 0;
    border-left: $grey-3 1px solid;
  }

  .panel__header {
    color: $copy-grey;
    height: $panel-header-height;
    padding-left: 10px;
    border-bottom: 1px solid $grey-4;
    display: flex;
    flex-direction: row;

    .panel__title {
      flex: 1 0 auto;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 13px;
      font-weight: bold;
      line-height: $panel-title-line-height;
    }

    .panel__edit__icon {
      margin-left: 0;
      align-self: flex-end;
      font-size: 18px;
      vertical-align: top;
      height: $panel-title-line-height;
      line-height: $panel-title-line-height;

      .fonticon {
        display: block;
      }
    }
  }

  .lightbox--responsive & {
    width: 100%;
  }

  @media screen and (min-width: 640px) {
    &.column {
      height: calc(100% - #{$lightbox-bar-height});
      top: $lightbox-bar-height;
    }
  }

  .panel__close_icon {
    float: right;
    font-size: $panel-close-icon-height;
    line-height: 38px;
  }

  &.lightbox__panel--left.column {
    > iframe.compass {
      border-width: 0;
      width: 0px;
      height: 0px;
      min-width: 100%;
      min-height: 100%;
    }
  }
}
</style>
<style lang="scss">
.vu-lightbox--appear-fast {
  animation: vu-fade-scale-in-075 0.3s ease-in;
}

.vu-lightbox--appear-faster {
  animation: vu-fade-scale-in-087 0.2s ease-in;
}

@keyframes vu-fade-scale-in-075 {
  from {
    transform: scale(0.75);
    opacity: 0;
  }
}

@keyframes vu-fade-scale-in-087 {
  from {
    transform: scale(0.87);
    opacity: 0;
  }
}
</style>
