<template>
  <vu-popover
    ref="popover"
    v-model:show="innerShow"
    :shift="shift || responsive"
    type="dropdownmenu popover"
    :attach="target"
    :side="position"
    :overlay="overlay"
    :animated="false"
    :checkPosition="checkPosition"
    :getNextPosition="getNextPosition"
    @update:show="updateShow"
    :arrow="false">
    <slot :active="innerShow"/>
    <template #body>
      <vu-dropdownmenu-items
        v-model:responsive="isResponsive"
        :dividedResponsiveItems="dividedResponsiveItems"
        :target="target"
        :items="items"
        :selected="value"
        @click-item="handleClick"
        @update:selected="open = false" />
    </template>
  </vu-popover>
</template>

<script>
/* eslint-disable no-unreachable */
/* eslint-disable no-else-return */
/* eslint-disable padded-blocks */

import detachable from '../mixins/detachable';
import showable from '../mixins/showable';
import vuDropdownmenuItems from './vu-dropdownmenu-items.vue';
import { positions, checkPosition, getNextPosition } from './vu-dropdownmenu-positions';
import vuPopover from './vu-popover.vue';

function validate(items, isValid = true) {
  let valid = isValid;

  items.forEach((item) => {
    // Text must be defined if not divider
    if (!item.text && !item.label
      && (!item.class || !item.class.includes('divider'))) valid = false;
    if (item.items) valid = validate(item.items, valid);
  });
  return valid;
}

export default {
  components: { vuDropdownmenuItems, vuPopover },
  name: 'vu-dropdownmenu',
  mixins: [showable, detachable],
  emits: ['close', 'click-item'],
  props: {
    value: {
      type: Array,
      default: () => ([]),
    },
    items: {
      type: Array,
      required: true,
      validator: validate,
    },
    preventDropup: {
      type: Boolean,
      default: false,
    },
    dividedResponsiveItems: {
      type: Boolean,
      default: false,
    },
    position: {
      type: String,
      required: false,
      default: 'bottom-left',
    },
    arrow: {
      type: Boolean,
      default: false,
    },
    overlay: {
      type: Boolean,
      default: false,
    },
    zIndex: {
      type: Number,
      default: () => 1000,
    },
    responsive: {
      type: Boolean,
      default: false,
    },
    shift: {
      type: Boolean,
      default: false,
    },
    closeOnClick: {
      type: Boolean,
      default: true,
    },
    positions: {
      type: Array,
      required: false,
      default: () => positions,
    },
    getNextPosition: {
      type: Function,
      required: false,
      default: getNextPosition,
    },
    checkPosition: {
      type: Function,
      required: false,
      default: checkPosition,
    },
  },
  data: () => ({
    open: false,
    innerResponsive: false,
  }),
  computed: {
    isResponsive: {
      get() { return this.innerResponsive || this.responsive; },
      set(value) { this.innerResponsive = value; },
    },
  },
  watch: {
    async items() {
      if (this.innerShow) {
        await this.$nextTick();
        this.$refs.popover.updatePosition();
      }
    },
  },
  methods: {
    handleClick(item) {
      if (item.handler) { item.handler(item); }
      this.$emit('click-item', item);
      this.updateShow(false);
    }, 
    updateShow(show) {
      if (!show) {
        if (this.closeOnClick) { 
          this.innerShow = false; 
          this.$emit('close');
        }
      } else {
        this.isResponsive = false; 
      }
    },
  },
};
</script>
