<template>
  <div class="vu-facets-bar" ref="container">
    <div class="facets-bar__inner" ref="inner">
      <!-- eslint-disable vue/valid-v-for -->
      <template v-for="item in visibleItems" :key="`${uuidv4()}`">
        <!-- eslint-enable vue/valid-v-for -->
        <vu-btn
          :class="[
            'facet',
            {
              default: item !== modelValue,
              'facet--selected': item === modelValue,
              'facet--unselected': item !== modelValue,
              'facet--icon-only': labelsTruncated && !(!activeLabelTruncated && item === modelValue),
            },
          ]"
          @click="$emit('update:modelValue', item)"
        >
          <template v-if="!labelsTruncated || (!activeLabelTruncated && item === modelValue)">
            <vu-icon v-if="item.icon" :icon="item.icon" :active="item === modelValue" />
            <span>{{ item.text }}</span>
          </template>
          <template v-else>
            <vu-popover type="tooltip" arrow>
              <template #default>
                <vu-icon v-if="item.icon" :icon="item.icon" />
              </template>
              <template #body>
                {{ item.text }}
              </template>
            </vu-popover>
          </template>
        </vu-btn>
      </template>
      <div v-if="labelsTruncated && !activeLabelTruncated" :style="[{ visibility: 'hidden' }, { width: `${activeFacetVsLongestFacet}+px` }]"></div>
      <!-- eslint-disable vue/no-mutating-props -->
      <vu-dropdownmenu
        v-if="visibleFacets"
        :shift="true"
        class="vu-facets-bar__dropdownmenu"
        :items="items"
        v-bind:model="modelValue"
        v-on:update:modelValue="(item) => $emit('update:modelValue', item)"
        @click-item="(item) => $emit('update:modelValue', item)"
      >
        <vu-icon-btn icon="menu-dot"></vu-icon-btn>
      </vu-dropdownmenu>
    </div>
  </div>
</template>

<script>
import { uuidv4 } from '../utils/guid';

export default {
  name: 'vu-facets-bar',
  emits: ['update:modelValue'],
  props: {
    modelValue: Object,
    items: {
      type: Array,
      default: () => [],
    },
  },
  data: () => ({
    uuidv4,
    labelsTruncated: false,
    activeLabelTruncated: false,
    startIndex: 0,
    hiddenFacets: 0,
    visibleFacets: 0,
    intxObs: undefined,
    intxObs2: undefined,
    intxObs3: undefined,
    resizeObs: undefined,
    activeFacetVsLongestFacet: 0,
  }),
  mounted() {
    this.intxObs = new IntersectionObserver(this.intersects, {
      root: this.$refs.container,
      threshold: 1,
    });
    this.intxObs2 = new IntersectionObserver(this.intersectsStep2, {
      root: this.$refs.container,
      threshold: 1,
    });
    this.intxObs3 = new IntersectionObserver(this.intersectsStep3, {
      root: this.$refs.container,
      threshold: 1,
    });
    this.resizeObs = new ResizeObserver(() => {
      this.hiddenFacets = 0;
      this.labelsTruncated = false;
      this.activeLabelTruncated = false;
      this.intxObs.observe();
    });
    this.intxObs.observe(this.$refs.inner);
  },
  beforeUnmount() {
    if (this.intxObs) {
      this.intxObs.disconnect();
    }
    if (this.intxObs2) {
      this.intxObs.disconnect();
    }
    if (this.intxObs3) {
      this.intxObs.disconnect();
    }
    if (this.resizeObs) {
      this.resizeObs.disconnect();
    }
    delete this.intxObs;
    delete this.intxObs2;
    delete this.intxObs3;
    delete this.resizeObs;
  },
  computed: {
    activeIndex() {
      return this.items.indexOf(this.modelValue);
    },
    visibleItems() {
      if (!this.hiddenFacets) {
        return this.items;
      }
      return this.items.slice(this.startIndex, this.startIndex + this.visibleFacets);
    },
    showPrepend() {
      return false;
    },
  },
  watch: {
    modelValue(value) {
      if (this.hiddenFacets) {
        const modelIndex = this.items.indexOf(value);
        let index = 0;
        if (modelIndex > this.visibleFacets - 1) {
          index = modelIndex - this.visibleFacets + 2;
        }
        this.startIndex = Math.min(index, this.items.length - this.visibleFacets);
      }
    },
    items(update, old) {
      if (update.length !== old.length || update.any((a, index) => a.text !== old[index].text)) {
        this.labelsTruncated = false;
        this.activeLabelTruncated = false;
        this.intxObs.observe();
      }
    },
  },
  methods: {
    async intersects(results) {
      this.intxObs.unobserve(this.$refs.inner);
      if (results && results[0] && results[0].intersectionRatio < 1) {
        const facets = this.$refs.inner.querySelectorAll('.facet');
        const activeFacet = this.$refs.inner.querySelector('.facet.facet--selected');
        const { width = 0 } = activeFacet || {};
        const maxWidth = Array.from(facets).reduce((a, max) => Math.max(a.width, max));
        this.activeFacetVsLongestFacet = maxWidth - width;
        this.labelsTruncated = true;
        await this.$nextTick();
        this.intxObs2.observe(this.$refs.inner);
      }
    },
    async intersectsStep2(results) {
      this.intxObs2.unobserve(this.$refs.inner);
      if (results && results[0] && results[0].intersectionRatio < 1) {
        this.activeLabelTruncated = true;
        this.activeFacetVsLongestFacet = 0;
        await this.$nextTick();
        const facets = this.$refs.inner.querySelectorAll('.facet');
        facets.forEach((element) => {
          this.intxObs3.observe(element);
        });
      }
    },
    // eslint-disable-next-line no-unused-vars
    async intersectsStep3(results) {
      results.forEach((element) => this.intxObs3.unobserve(element.target));
      this.hiddenFacets = results.filter((res) => res.intersectionRatio < 1).length;
      if (this.hiddenFacets > 0) {
        this.visibleFacets = this.items.length - this.hiddenFacets;
      }
      // Check if last item overflows 
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../styles/_variables.scss';

.vu-facets-bar {
  position: relative;
  width: calc(100% - 20px);
  margin: 0 10px;
  overflow: hidden;
  border-top: 1px $grey-4 solid;
  border-bottom: 1px $grey-4 solid;
  padding: 3px 10px;
  height: 40px;
}

.facet-bar__inner {
  display: flex;
}

.facet {
  border-radius: 4px;
  padding: 0px;
  height: 32px;
  border: 0;
  font-size: 16px;
  min-width: 0;

  &:not(:first-child) {
    margin: 0 0 0 10px;
  }

  .vu-icon {
    margin-left: 4px;
  }

  span {
    line-height: 32px;
  }

  span:last-of-type {
    margin: 0 10px;
  }

  .vu-icon {
    width: auto;
    font-size: 18px;
    margin-right: 0;
    margin-left: 4px;
    color: $grey-6;

    + span {
      margin-left: 8px;
    }
  }

  &.facet--unselected {
    background: white;
    color: $grey-6;

    &:hover {
      background: $grey-3;
      color: $grey-7;

      .vu-icon {
        color: $grey-7;
      }
    }
  }

  &.facet--unselected:active,
  &.facet--selected:active,
  &.facet--selected {
    background: $blue-3;

    &,
    .vu-icon {
      color: white;
    }

    &:active {
      background: $blue-5;
    }
  }
}
</style>
<style lang="scss">
.vu-facets-bar .vu-facets-bar__dropdownmenu {
  position: absolute;
  right: 0;
  top: 0;
  height: 38px;

  .vu-icon {
    font-size: 18px;
  }
}
</style>
