<template>
  <div class="form-group"
    :class="classes">
    <label class="control-label" v-if="label.length"
      >{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <input
      v-bind="$attrs"
      :value="value"
      :placeholder="placeholder"
      :disabled="disabled"
      :type="type"
      class="form-control"
      @input="({target}) => { if(!vuInputComposition) { target.composing = false } $emit('update:modelValue', target.value); }"
    />
    <span
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span v-if="helper.length" class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>

<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';

import { RegistrableInput } from '../mixins/registrable';

export default {
  name: 'vu-input',
  inheritAttrs: false,
  inject: {
    vuInputComposition: {
      default: false,
    },
  },
  mixins: [inputable, validatable, disablable, RegistrableInput],
};
</script>

<style scoped>
input.form-control {
  padding-left: 10px;
  padding-right: 10px;
}
</style>
