export const RegistrableForm = {
  data: () => ({
    inputs: [],
  }),
  exposes: ['validate'],
  provide() {
    return {
      inputs: this.inputs,
    };
  },
  methods: {
    validate(blank) {
      return this.inputs
        .map((el) => el.validate(undefined, blank))
        .reduce((acc, el) => acc && el, true);
    },
  },
};

export const RegistrableInput = {
  inject: {
    inputs: {
      default: () => '',
    },
  },
  created() {
    if (typeof this.inputs === 'object') {
      this.inputs.push(this);
    }
  },
  beforeUnmount() {
    if (typeof this.inputs === 'object') {
      this.inputs.splice(this.inputs.indexOf(this), 1);
    }
  },
};
