
const pick = (obj, ...keys) => Object.fromEntries(
  keys
    .filter((key) => key in obj)
    .map((key) => [key, obj[key]]),
);

const pickNRename = (obj, ...keys) => Object.fromEntries(
  keys
    .filter(({ key }) => key in obj)
    .map(({ key, newName = key }) => [newName, obj[key]]),
);

export default pick;
export { pick, pickNRename };
