<template>
  <div class="vu-thumbnail item" 
    :class="classes">
    <div
      class="thumbnail-wrap"
      style="position: relative">
      <vu-image :src="src" :lazy="vuCollectionLazyImages" :aspectRatio="'200/150'"
        contain>
      </vu-image>
      <div
        v-if="active"
        class="thumbnail__thumb"
      ></div>
      <vu-icon
        v-if="selectable || selected"
        icon="check"
        class="thumbnail__check" />
      <vu-tile
        thumbnail
        :title="title"
        :type="type"
        :author="author"
        :date="date"
        :actions="actions"
        :customMetaData="customMetaData"
        hideStatusBar
        @click-action="getListenersFromAttrs($attrs)['onClickAction']">
      </vu-tile>
      <div class="thumbnail__content">
        {{ text }}
      </div>
      <vu-status-bar v-if="!hideStatusBar" :status="status"></vu-status-bar>
    </div>
  </div>
</template>
<script>
import { getListenersFromAttrs } from '../../utils/vue';

export default {
  name: 'vu-thumbnail',
  inject: ['vuCollectionLazyImages'],
  props: {
    id: {
      type: String, 
      required: true,
    },
    src: String,
    type: String,
    active: Boolean,
    actions: Array,
    title: String,
    text: String,
    selected: Boolean,
    selectable: Boolean,
    author: String,
    date: Date,
    customMetaData: String,
    status: Array,
    hideStatusBar: Boolean,
  },
  data: () => ({
    getListenersFromAttrs,
  }),
  computed: {
    classes() {
      return {
        'thumbnail--selectable': this.selectable || this.selected,
        'thumbnail--selected': this.selected,
        'thumbnail--active': this.active,
      };
    },
  },
};
</script>
<style lang="scss" scoped>
  @import 'select';

  $name: 'thumbnail';

  @include selectable(#{$name}, 'wall');
  @include active(#{$name});

  .vu-#{$name} {
    .thumbnail-wrap {
      display: flex;
      flex-direction: column;
    }

    .vu-image {
      flex: 0 0 auto;
      width: 100%;
      border-bottom: 1px solid $grey-3;
    }

    .vu-tile + .thumbnail__content {
      border-top: 1px solid $grey-3;
    }

    .thumbnail__content {
      flex: 0 0 auto;
      border-top: 1px solid $grey-3;
      padding: 14px 10px;
      min-height: 34px;
      font-size: 12px;
    }

    .vu-tile {
      flex: 0 0 70px;
      width: 100%;
      border: none;
    }

    .vu-status-bar {
      border-top: 1px solid $grey-3;
    }

    min-width: 200px;
    max-width: 420px;

    :deep(.vu-tile.tile--thumbnail) {
      min-width: 200px;
    } 
  }
</style>
<style lang="scss">
  @import '../../styles/variables';

  $name: 'thumbnail';

  .vu-#{$name} {
    --vu-thumbnail-check-top: 10px;
    --vu-thumbnail-check-right: 10px;
  };
</style>
