<template>
  <div class="vu-mobile-dialog">
    <div class="vu-mobile-dialog__header">
      <slot name="mobile-dialog-header">
        <div class="vu-mobile-dialog__header__default">
          <vu-icon-btn
            :icon="_backIcon"
            :class="backClasses"
            class="vu-mobile-dialog__header_back topbar"
            v-tooltip.bottom="backIconTooltip"
            @click="$emit('close')"
          />
          <div class="vu-label-wrap" style="overflow: hidden">
            <label v-tooltip.bottom="title">{{ title }}</label>
          </div>
          <vu-icon-btn
            :icon="_icon"
            :class="nextClasses"
            :disabled="nextIconDisabled"
            class="vu-mobile-dialog__header_next topbar"
            v-tooltip.bottom="nextIconTooltip"
            @click="$emit('confirm')"
          />
        </div>
      </slot>
    </div>
    <div class="vu-mobile-dialog__content" :class="`vu-mobile-dialog__content--${scrollable ? '' : 'non-'}scrollable`">
      <vu-scroller v-if="scrollable">
        <slot />
      </vu-scroller>
      <template v-else>
        <slot />
      </template>
    </div>
  </div>
</template>
<script>
export default {
  name: 'vu-mobile-dialog',
  emits: ['close', 'confirm'],
  props: {
    title: {
      type: String,
      default: '',
    },
    backIcon: {
      type: String,
      default: 'close',
    },
    backIconTooltip: {
      type: String,
      default: 'Close',
    },
    nextIcon: {
      type: String,
      default: 'check',
    },
    nextIconTooltip: {
      type: String,
      default: 'OK',
    },
    scrollable: {
      type: Boolean,
      default: true,
    },
    customNextIcon: {
      type: Boolean,
    },
    customBackIcon: {
      type: Boolean,
    },
    nextIconDisabled: {
      type: Boolean,
    },
  },
  computed: {
    _backIcon() {
      return !this.customBackIcon ? (['chevron-left', 'close'].includes(this.backIcon) ? this.backIcon : '-') : this.backIcon;
    },
    _icon() {
      return !this.customNextIcon ? (['chevron-right', 'send', 'check'].includes(this.nextIcon) ? this.nextIcon : '-') : this.nextIcon;
    },
    backClasses() {
      return [this._backIcon === 'chevron-left' ? 'chevron' : ''];
    },
    nextClasses() {
      return [this._icon === 'chevron-right' ? 'chevron' : ''];
    },
  },
};
</script>
