  background-color: white;
  border-bottom: 1px solid $grey-4;

  &__default {
    flex: 1 0 44px;
    display: flex;
    flex-direction: row;
    max-width: 100%;
  }
}

.vu-mobile-dialog__content {
  background-color: var(--mobile-modal-bgc);
  flex: 1 0 auto;
  height: calc(100% - 45px);

  &--non-scrollable,
  &--scrollable :deep(.vu-scroll-container__inner) {
    padding: 14px;
  }
}

.vu-mobile-dialog__header__default {
  .vu-icon-btn {
    flex-shrink: 0;
  }

  .vu-label-wrap {
    max-width: 100%;
    overflow: hidden;
    white-space: nowrap;
