<template>
  <div class="dropdown-menu dropdown-menu-root dropdown-root"
    :style="[{ 'zIndex': zIndex }]"
    :class="classes">
    <ul class="dropdown-menu-wrap"
      :class="{ 'dropdown-menu-icons' : hasIcons }">
      <li v-if="responsive && stack.length"
      class="item item-back">
        <vu-icon icon="left-open"
          class="back-item"
          @click.stop="onBackItemClick" />
        <span
          class="item-text">{{ _parent.text }}</span>
      </li>
      <template v-for="item in _items">
        <li
          v-if="!item.class || (!item.class.includes('header') && !item.class.includes('divider'))"
          :key="item.text || item.label"
          :class="['item', {
            'item-submenu': item.items,
            selectable: !item.disabled && item.selectable || item.selected || selected.includes(item),
            selected: item.selected || selected.includes(item),
            hidden: item.hidden,
            disabled: item.disabled,
            'hide-responsive-divider': !dividedResponsiveItems,
          }, item.class]"
          @click.stop="item.items && responsive && !dividedResponsiveItems ? onNextItemClick(item) : onItemClick(item)">
          <slot :item="item">
            <vu-icon v-if="item.fonticon" :icon="item.fonticon" />
            <span class="item-text">{{ item.text || item.label }}</span>
          </slot>
          <div v-if="item.items"
            class="next-icon"
            @click.stop="onNextItemClick(item)">
            <span class="divider"></span>
            <vu-icon icon="right-open" />
          </div>
          <vu-dropdownmenu-items
            v-if="!responsive && item.items"
            :target="target"
            :items="item.items"
            :selected="selected"
            @click-item="onItemClick"
            @update:selected="$emit('update:selected', $event)" 
            @update:responsive="$emit('update:responsive', $event)"
            />
        </li>
        <li v-else
          :key="item.text || item.label || uuid()"
          :class="item.class">
          <span v-if="item.class !== 'divider'" class="item-text">{{ item.text || item.label }}</span>
        </li>
      </template>
    </ul>
  </div>
</template>

<script>
import VuIcon from './vu-icon.vue';
import uuid from '../utils/uuid';

function validate(items, isValid = true) {
  let valid = isValid;

  items.forEach((item) => {
    // Text must be defined if not divider
    if (!item.text && !item.label
      && (!item.class || !item.class.includes('divider'))) valid = false;
    if (item.items) valid = validate(item.items, valid);
  });
  return valid;
}

export default {
  name: 'vu-dropdownmenu-items',
  components: { VuIcon },
  emits: ['update:responsive', 'click-item'],
  props: {
    target: {
      type: HTMLElement,
      required: false,
    },
    items: {
      type: Array,
      required: true,
      validator: validate,
    },
    selected: {
      type: Array,
      required: true,
    },
    zIndex: {
      type: Number,
      default: 1000,
    },
    responsive: {
      type: Boolean,
      default: false,
    },
    dividedResponsiveItems: {
      type: Boolean,
      default: false,
    },
    disableResponsive: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    stack: [],
    left: false,
    uuid,
    root: false,
    parent: {},
  }),
  computed: {
    classes() {
      return {
        'open-left': this.left,
        'responsive-menu': this.responsive,
        'dropdown-menu-responsive-wrap': this.responsive,
      };
    },
    hasIcons() {
      return this.items && this.items.some((item) => item.fonticon);
    },
    _items() {
      return this.stack[this.stack.length - 1] || this.items;
    },
    _parent() {
      return (this.stack[this.stack.length - 2] || this.items)
        // JSON.stringify is a bypass to compare the src object with the proxyfied same object
        // Any other wau to do that ????
        .find((el) => JSON.stringify(el.items) === JSON.stringify(this._items)); 
    },
  },
  async mounted() {
    if (this.disableResponsive) {
      return;
    }
    await this.$nextTick();
    const options = {
      root: this.target,
      threshold: 1.0,
    };

    const rootBCR = this.target.getBoundingClientRect();
    const observer = new IntersectionObserver(async ([entry]) => {
      observer.unobserve(this.$el);
      const elBCR = entry.target.getBoundingClientRect();

      if (rootBCR.right < elBCR.right && !this.left) { // right part of the dropdown is hidden
        // the menu isn't 100% visible, try to display it left
        this.left = true;
        await this.$nextTick();
        observer.observe(this.$el); // observe again to check the dropdown is visible with this new setting
      } else if (rootBCR.left > elBCR.left && this.left) { // left part of the dropdown is hidden
        // the menu isn't 100% visible, try responsive mode
        this.$emit('update:responsive', true);
      }
    }, options);
    await this.$nextTick(); 
    observer.observe(this.$el);
  },
  methods: {
    toggleSelected(item) {
      const selected = this.selected.slice(0);

      if (item.selected || this.selected.includes(item)) {
        selected.splice(this.selected.indexOf(item), 1);
      } else {
        selected.push(item);
      }

      return selected;
    },
    onItemClick(item) {
      if (!item.disabled && (item.selectable || item.selected || this.selected.includes(item))) {
        this.$emit('update:selected', this.toggleSelected(item));
      }
      this.$emit('click-item', item);
    },
    onNextItemClick(item) {
      if (this.responsive) {
        this.stack.push(item.items);
      }
    },
    onBackItemClick() {
      this.stack.pop();
    },
  },
};
</script>

<style lang="scss" scoped>

  .dropdown-menu {
    .item {
      height: 44px;
    }

    .item-back .item-text {
      padding: 0 14px;
    }
  }

  .dropdown-menu.responsive-menu .item-submenu .next-icon {
    padding-right: 9px;
  }

  .dropdown-menu-responsive-wrap {
    top: 0;
  }
  
  .dropdown-menu:not(.responsive-menu) .dropdown-menu {
    top: -1px;
    &:not(.open-left) {
      left: 100%;
    }

    &.open-left {
      right: 100%;
    }
  }

  .dropdown-menu li > .dropdown-menu {
    visibility: hidden;
  }
  .dropdown-menu li:hover > .dropdown-menu {
    visibility: visible;
  }

  .vu-dropdown-menu__activator {
    display: inline-block;
    position: relative;
  }

  .dropdown-menu-icons > span > span > .item {
    padding-left: 0px;

    .item-text:nth-child(1) {
      padding-left: 14px;
    }
  }

  .dropdown-menu.dropdown-menu-root {
    margin: 0px;
  }

  .dropdown-menu.has-arrow:not(.dropup) {
    margin-top: 14px;
  }
  .dropdown-menu.has-arrow.dropup {
    margin-top: -14px;
  }

  .dropdown-root-arrow {
    position: absolute;
    top: calc(100%);
    left: calc(50% - 13px);
    display: block;
    font-size: 20px;

    &.upwards {
      top: unset;
      bottom: calc(100% + 14px);
    }

    &:before, &:after {
      content: "";
      border-style: solid;
      width: 0;
      height: 0;
      position: absolute;
    }

    &:before {
      border-width: 0 14px 15px 13px;
      border-color: rgba(154,154,154,0.44) transparent;
      transform: translateX(-1px) translateY(-1px);
    }

    &:after {
      border-color: #fff transparent;
      border-width: 0 12px 14px 12px;
    }
  }
</style>
