/* eslint-disable no-param-reassign */
// eslint-disable-next-line import/no-extraneous-dependencies
import { createVNode, render } from 'vue';
import Tooltip from '../components/vu-tooltip.vue';
import { isAndroid, isIpad } from '../utils/client';
import { setPosition } from '../utils/positionable';

let configDisableOnDevices = null;

const setConfig = ({ disableTooltipsOnDevices }) => {
  configDisableOnDevices = disableTooltipsOnDevices;
};

// Largely inspired by UIKIT.
const _setPosition = (side, target, tooltip, container) => {
  const targetRect = target.getBoundingClientRect();
  const { 
    // eslint-disable-next-line no-unused-vars
    left, right, top, shiftX, offset,
  } = setPosition(side,
    targetRect,
    tooltip.getBoundingClientRect(),
    container.getBoundingClientRect(), {}, true);

  tooltip.style.top = `${top}px`;
  tooltip.style.left = `${left}px`;

  const arrow = tooltip.querySelector('.tooltip-arrow') || { style: { } };

  if (shiftX > 0) { 
    arrow.style.right = `${ shiftX - 5 }px`;
    arrow.style.left = 'initial';
  } else if (shiftX < 0) {
    arrow.style.left = `${ targetRect.left - left + target.clientWidth / 2 }px`;
    arrow.style.right = 'initial';
  }
  
  return true;
};

const getSide = (modifiers) => {
  switch (true) {
    case modifiers.left:
      return 'left';
    case modifiers.right:
      return 'right';
    case modifiers.bottom:
      return 'bottom';
    default:
      return 'top';
  }
};

const show = async (element, options) => {
  if (!options.value) {
    return;
  }
  if (options.modifiers.ellipsis && element.offsetWidth >= element.scrollWidth) {
    return;
  }
  const side = getSide(options.modifiers);

  render(element.tooltip, document.body);
  element.tooltip.component.props.show = true;
  element.tooltip.component.props.side = side;

  await new Promise((r) => setTimeout(r, 1));
  
  _setPosition(side, element, element.tooltip.el, document.body);
};

const hide = (element) => {
  const { tooltip: { component } } = element;
  component.props.show = false;
};

const update = async (element, options, vnode) => {
  const { tooltip } = element;
  if (tooltip) {
    const { component } = tooltip;
    tooltip.props.text = options.value;
    if (component) { component.props.text = options.value; }
    if (component?.props?.show) {
      const side = getSide(options.modifiers);
      await new Promise((r) => setTimeout(r, 1));
      _setPosition(side, vnode.el, tooltip.el, document.body);
    }
  }
};

// eslint-disable-next-line no-unused-vars
export default {
  setConfig,
  mounted(element, options, vnode) {
    if ((configDisableOnDevices
      // eslint-disable-next-line no-undef
      && (isAndroid(navigator) || isIpad(navigator)))
      || options.disabled) {
      return;
    }

    const tooltip = createVNode({ ...Tooltip }, {
      type: options.modifiers.popover ? 'popover' : 'tooltip',
      text: options.value,
    });
    element.tooltip = tooltip;

    if (options.modifiers.click) {
      // TODO use utils/positionable.js
      element.addEventListener('click', () => {
        if (element?.tooltip?.component?.props?.show) {
          hide(element, options, vnode);
        } else {
          show(element, options, vnode);
        }
      });
    } else {
      // default behavior opens tooltip on mousehober
      element.addEventListener('mouseenter', show.bind(null, element, options, vnode));
      element.addEventListener('mouseleave', hide.bind(null, element, options, vnode));
    }
  },
  updated(el, options, vnode) {
    if (options.value !== options.oldValue) {
      update(el, options, vnode);
    }
  },
  beforeUnmount(el) {
    if (el.tooltip) {
      const { tooltip } = el;
      if (tooltip) {
        // eslint-disable-next-line no-unused-expressions
        tooltip?.component?.el?.remove();
        // eslint-disable-next-line no-unused-expressions
        tooltip?.component?.vnode?.el?.remove();
      }
    }
  },
};
