const positions = ['top', 'top-right', 'bottom-right', 'bottom', 'bottom-left', 'top-left'];

// Returns false when position is incorrect
// Dropdownmenu can overflow to the left and to the right as it has responsive mode
const checkPosition = ({ intersectionRatio, intersectionRect, elementRect }) => intersectionRatio < 1 
  && !(intersectionRect.top < elementRect.top
  || intersectionRect.bottom > elementRect.bottom
  );

const getNextPosition = (currentPosition, requestedPosition, requestedOrder, attempts) => {
  if (attempts.length === 0) {
    return requestedPosition.includes('top') ? requestedPosition.replace('top', 'bottom') : requestedPosition.replace('bottom', 'top');
  }
  return requestedPosition;
};

export default positions;
export {  
  positions,
  checkPosition,
  getNextPosition,
};
