// eslint-disable-next-line import/no-extraneous-dependencies
import {
  ref, onMounted, onUnmounted, computed, 
} from 'vue';

export default function useStickyUsingIntersection(
  isEnabled,
  el,
  className,
) {
  const previousVisible = ref(false);
  const previousObserver = ref({});
  const previousElement = ref({});

  const observer = ref({});
  const targets = ref([]);
  const visibilities = ref(new WeakMap());
  const anyVisible = computed(() => { const intermediate = targets.value.map(((target) => visibilities.value.get(target))); return intermediate.reduce((prev, curr) => prev || curr, false); });
  
  const stick = computed(
    () => (!previousElement.value || previousVisible.value === false) && anyVisible.value,
  );
  
  onMounted(() => {
    if (isEnabled) {
      observer.value = new IntersectionObserver(
        (entries) => {
          entries.forEach(({ intersectionRatio, target }) => {
            visibilities.value.set(target, intersectionRatio > 0);
          });
        },
      );
      previousObserver.value = new IntersectionObserver(
        (entries) => {
          previousVisible.value = entries[0].intersectionRatio > 0;
        },
      );
      let { nextElementSibling } = el.value;
      while (nextElementSibling 
        && nextElementSibling.className.indexOf(className) === -1) {
        observer.value.observe(nextElementSibling);
        targets.value.push(nextElementSibling);
        nextElementSibling = nextElementSibling.nextElementSibling;
      }
      if (el?.value?.previousElementSibling) {
        previousElement.value = el.value.previousElementSibling;
        previousObserver.value.observe(el.value.previousElementSibling);
      }
    }
  });

  onUnmounted(() => {
    if (observer.value) {
      observer.value.disconnect();
    }
    if (previousObserver.value) {
      previousObserver.value.disconnect();
    }
    delete observer.value;
    delete previousObserver.value;
  });

  return { stick };
}
