<template>
  <div class="vu-status-bar"
    ref="container"
    :class="{ 'status-bar--constrained' : constrained }">
    <div class="status-bar__inner" ref="inner">
      <template v-for="(item) in visibleItems" :key="item.id">
        <vu-badge
          :icon="item.icon"
          :text="item.text || item.amount && units(item.amount) || ''"
          :color="item.color || 'copy-grey'"
          :value="item.value"
          :togglable='false'
          :style="[item.amount && item.icon ? 'min-width: 45px': '']"
          v-tooltip.hover="item.tooltip || item.text || item.amount || ''"
          >
        </vu-badge>
      </template>
      <vu-popover v-if="overflows"
         type="tooltip"
        contentClass="vu-status-bar"
        shift arrow>
        <template #default>
          <vu-icon icon="menu-dot" style="transform: rotate(90deg)"></vu-icon>
        </template>
        <template #body>
          <template v-for="(item) in hiddenItems" :key="item.id">
            <vu-badge
              :icon="item.icon"
              :text="item.text || `${item.amount}` || ''"
              :color="item.color || 'copy-grey'"
              :value="item.value"
              :togglable='false'>
            </vu-badge>
          </template>
        </template>
      </vu-popover>
    </div>
  </div>
</template>
<script>
export default {
  name: 'vu-status-bar',
  props: {
    items: {
      type: Array,
      default: () => [],
    },
    constrained: Boolean,
  },
  data() {
    return {
      overflows: false,
      ellipsis: false,
      intObs: null,
      intObs2: null,
      visibleAmount: 0,
    };
  },

  mounted() {
    this.watchSize();
  },

  computed: {
    visibleItems() {
      return this.items.slice(0, this.visibleAmount);
    },
    hiddenItems() {
      if (!this.overflows) return [];
      return this.items.slice(this.visibleAmount);
    },
  },

  watch: {
    items: {
      immediate: true,
      // eslint-disable-next-line object-shorthand, func-names
      handler: function (items) {
        this.visibleAmount = items.length;
        this.ellipsis = false;
        this.overflows = false;
        if (this.$el) {
          this.$nextTick(() => this.watchSize());
        }
      },
    },
  },
  methods: {
    watchSize() {
      this.intObs = new IntersectionObserver(this.intersects, {
        root: this.$refs.container,
        threshold: 1,
      });
      this.intObs.observe(this.$refs.inner);
      this.intObs2 = new IntersectionObserver(this.intersects2, {
        root: this.$refs.inner,
        threshold: 1,
      });
    },
    async intersects() {
      this.intObs.disconnect();
      this.ellipsis = true;
      const items = this.$refs.inner.querySelectorAll('.vu-badge');
      await this.$nextTick();
      items.forEach((element) => {
        this.intObs2.observe(element);
      });
    },
    intersects2(results) {
      const partiallyVisibleElements = results.filter((res) => res.intersectionRatio < 1);
      let { length } = partiallyVisibleElements;
      // eslint-disable-next-line no-unused-vars
      if (length) {
        const rec = this.$refs.inner.getBoundingClientRect();
        const { right } = rec;
        const last = partiallyVisibleElements.shift();
        // allow for overflow icon // 22px hardcoded
        if (last && right - last.target.getBoundingClientRect().left - 22 < 0) {
          length += 1;
        }
        this.visibleAmount -= length;
        this.overflows = true; 
      }
      this.intObs2.disconnect();
    },
    units(amount) {
      if (this.ellipsis) return amount > 99 ? '99+' : `${amount}`;
      return `${amount}`;
    },
    destroyed() {
      if (this.intObs1) delete this.intObs1;
      if (this.intObs2) delete this.intObs2;
    },
  },
};
</script>
