  @import '../../styles/_variables.scss';
  
  $name: 'status-bar';

  .vu-#{$name} {
    height: 28px;
    padding: 0 10px;
    background: $grey-2;
    
    .status-bar__inner {
      height: 100%;
      display: flex;
      gap: 6px;
      max-width: calc(100% - 20px);
      align-items: center;
      align-content: center;
      flex-wrap: nowrap;
      overflow: hidden;
    }

    &.#{$name}--constrained .status-bar__inner {
      max-width: 176px;
    }

    .vu-badge {
      height: calc(100% - 6px);
      font-size: 12px;
      flex: 0 1 auto;
      border-radius: 4px;

    }
    
    &.tooltip-root .vu-badge {
      height: 20px;
    }

    .vu-badge {
      :deep(.badge-icon) {
        margin-left: 4px;
        // smaller than badges
        margin-top: -4px;
      }
      :deep(.badge-icon:first-child:last-child) {
        margin: -4px 2px 0 2px;
        width: 14px;
      }
      :deep(.badge-content) {
        margin-top: -4px;
        margin-left: 3px;
        padding: 0;
        padding-right: 3px;
      }
    }

    :deep(.vu-popover__activator) {
      flex: 0 0 auto;
      height: calc(100% - 4px);
      line-height: 21px;
    }

    :deep(.badge:not(.badge-closable) .badge-icon + .badge-content) {
      width: calc(100% - 1em - 4px - 3px);
    }
  }
</style>
