<template>
  <div
    class="vu-tree-view-item"
    :class="{
      'vu-tree-view-item--selected': isSelected,
      'vu-tree-view-item--unselected': !isSelected,
      'vu-tree-view-item--main': main,
      'vu-tree-view-item--child': !main,
      'vu-tree-view-item--chevron-icon': vuTreeIcon === 'chevron',
    }"
    :style="{
      'padding-left': `${calcLeftPadding}px`,
    }"
    @click="onClick"
  >
    <vu-icon-btn v-if="showTreeIcon" :icon="getTreeIconClass" class="vu-tree-view-item__tree-icon" @click="$emit('expand', item)" ref="treeIcon" />
    <template v-else-if="isLoading">
      <div class="vu-tree-view-item__tree-icon-loading" ref="loadingSpinner">
        <slot name="itemLoading">
          <svg class="vu-spin" viewBox="25 25 50 50">
            <circle class="path" cx="50" cy="50" r="20" fill="none" stroke="currentColor" stroke-width="5" stroke-miterlimit="10"></circle>
          </svg>
        </slot>
      </div>
    </template>
    <div v-else class="vu-tree-view-item__tree-icon-placeholder"></div>
    <vu-icon-btn v-if="item.icon" class="vu-tree-view-item__type-icon" color="default-inactive" :icon="item.icon" />
    <div v-else-if="siblingsHaveNoType" class="vu-tree-view-item__type-icon-placeholder"></div>
    <slot :name="'item-' + item.type || 'default'">
      <div class="vu-tree-view-item__label" v-tooltip.ellipsis="item.label">{{ item.label }}</div>
    </slot>
  </div>
  <template v-if="hasItems && isExpanded">
    <template v-for="item in item.items" :key="`${item.id}`">
      <vu-tree-view-item
        :item="item"
        :depth="depth + 1"
        :leftPadding="calcLeftPadding"
        :selected="selected"
        :loading="loading"
        :expanded="expanded"
        :siblingsHaveNoType="anyChildrenHasIcon"
        @load-complete="(i) => $emit('load-complete', i)"
        @expand="(i) => $emit('expand', i)"
        @select="(i) => $emit('select', i)"
      />
    </template>
  </template>
</template>

<script>
import { validateTreeViewItem } from '../model/treeViewItem';
import guid from '../utils/uuid';
import loadable from '../mixins/loadable';

export default {
  name: 'vu-tree-view-item',
  mixins: [loadable],
  emits: ['load-complete', 'click', 'expand', 'select'],
  props: {
    selected: {
      type: Array,
      default: () => [],
    },
    expanded: {
      type: Array,
      default: () => [],
    },
    loading: {
      type: Array,
      default: () => [],
    },
    depth: {
      type: Number,
      default: () => 0,
    },
    hover: {
      type: Boolean,
      default: false,
    },
    siblingsHaveNoType: {
      type: Boolean,
      default: false,
    },
    item: {
      type: Object,
      validator: validateTreeViewItem(),
    },
    main: {
      type: Boolean,
      default: false,
    },
    leftPadding: {
      type: Number,
      default: 0,
    },
  },
  inject: {
    vuTreeViewLazy: {
      default: false,
    },
    vuTreeViewLeftPadBase: {
      default: 38,
    },
    vuTreeViewLeftPadFunc: {
      type: Function,
      default: undefined,
    },
    vuTreeViewLeftPadReduce: {
      type: Boolean,
      default: false,
    },
    vuTreeIcon: {
      type: String,
      default: 'expand',
    },
  },
  data: () => ({
    guid,
  }),
  watch: {
    item: {
      deep: true,
      handler(val) {
        if (this.isLoading) {
          this.$emit('load-complete', val);
        }
      },
    },
  },
  created() {
    if (this.item.expanded && !this.isExpanded) {
      this.$emit('expand', this.item);
    }
    if (this.item.selected && !this.isSelected) {
      this.$emit('select', this.item);
    }
  },
  computed: {
    otherSlots() {
      return Object.fromEntries(this.$slots.filter((s) => s.startsWith('item-')));
    },
    showTreeIcon() {
      return (
        // eslint-disable-next-line operator-linebreak
        this.hasItems || (this.vuTreeViewLazy && !this.item.leaf && this.item.items === undefined && !this.isLoading)
      );
    },
    hasItems() {
      return this.item.items && this.item.items.length > 0;
    },
    isSelected() {
      return this.selected.includes(this.item);
    },
    isExpanded() {
      return this.expanded.includes(this.item);
    },
    isLoading() {
      return this.vuTreeViewLazy && this.loading.includes(this.item);
    },
    anyChildrenHasIcon() {
      return this.hasItems && this.item.items.some((a) => a.icon !== undefined);
    },
    getTreeIconClass() {
      return this.isExpanded ? `${this.vuTreeIcon}-down` : `${this.vuTreeIcon}-right`;
    },
    calcLeftPadding() {
      if (this.vuTreeViewLeftPadFunc) {
        return this.vuTreeViewLeftPadFunc(this.depth, this.leftPadding);
      }
      if (!this.depth) {
        return 0;
      }
      if (this.vuTreeViewLeftPadReduce) {
        return Math.max(this.leftPadding + this.vuTreeViewLeftPadBase - 6 * this.depth, this.leftPadding + 6);
      }
      return this.leftPadding + this.vuTreeViewLeftPadBase;
    },
  },
  methods: {
    onClick(e) {
      const toExclude = [this.$refs.loadingSpinner?.$el, this.$refs.treeIcon?.$el].filter((a) => a);
      if (toExclude.every((el) => !el.contains(e.target))) {
        this.$emit('select', this.item);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
@import '../styles/variables';

.vu-tree-view-item {
  border-left: 5px solid transparent;
  display: flex;

  &--selected {
    border-left-color: $blue-3;
    background: $grey-1;

    .vu-tree-view-item__label {
      color: $grey-8;
    }

    .vu-tree-view-item__type-icon {
      color: $blue-3;
    }
  }

  &--unselected:hover {
    background-color: $grey-2;

    .vu-tree-view-item__type-icon,
    .vu-tree-view-item__label {
      color: $grey-8;
    }
  }

  .vu-icon-btn {
    height: 100%;
    font-size: 16px;

    :deep(.vu-icon) {
      line-height: inherit;
    }
  }

  &__tree-icon.vu-icon-btn {
    font-size: 12px;
    min-width: 38px;
  }

  &__tree-icon-loading,
  &__tree-icon-placeholder {
    width: 38px;
    min-width: 38px;
  }

  &__type-icon.vu-icon-btn,
  &__type-icon-placeholder {
    width: 22px;
    min-width: 22px;
    margin-right: 10px;
  }

  &__label {
    line-height: 38px;
    font-size: 14px;
    font-family: Arial;
    color: $grey-7;

    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &--main {
    line-height: 48px;
    height: 48px;

    .vu-icon-btn {
      height: 48px;
      line-height: 48px;
    }

    .vu-tree-view-item__label {
      line-height: 48px;
      font-weight: bold;
      color: $grey-7;
    }
  }

  &:hover {
    background: grey-2;
  }

  //TD
  .vu-spin {
    color: $blue-3;
    height: 38px;
    width: 24px;
    vertical-align: middle;
    margin-left: 8px;
    font-size: 20px;
    width: 1em;
    min-width: 1em;
  }

  .vu-spin {
    animation: vu-spin 2s linear infinite;
    transform-origin: center center;
  }

  .vu-spin .path {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    animation: vu-spin-mat-dash 1.5s ease-in-out infinite;
  }

  @keyframes vu-spin {
    0% {
      transform: rotate3d(0, 0, 1, 0);
    }

    25% {
      transform: rotate3d(0, 0, 1, 90deg);
    }

    50% {
      transform: rotate3d(0, 0, 1, 180deg);
    }

    75% {
      transform: rotate3d(0, 0, 1, 270deg);
    }

    to {
      transform: rotate3d(0, 0, 1, 359deg);
    }
  }

  @keyframes vu-spin-mat-dash {
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }

    50% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -35px;
    }

    to {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -124px;
    }
  }
}
</style>
