<template>
  <div class="vu-image" :style="imageStyle">
    <div class="vu-image__sizer" :style="imageSizerStyle"></div>
    <vu-lazy v-if="lazy" @intersect="loadImage()">
      <template #default>
        <div
          class="vu-image__image"
          :class="imageClasses"
          :style="[!isLoading ? { backgroundImage: `url(${src})` } : '']"
          style="background-position: center center"
        ></div>
      </template>
    </vu-lazy>
    <div
      v-else
      class="vu-image__image"
      :class="imageClasses"
      :style="[!isLoading ? { backgroundImage: `url(${src})` } : '']"
      style="background-position: center center"
    ></div>
    <div class="vu-image__fill"></div>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'vu-image',
  props: {
    lazy: {
      type: Boolean,
      required: false,
    },
    src: {
      type: String,
      required: true,
    },
    height: [Number, String],
    maxHeight: [Number, String],
    maxWidth: [Number, String],
    minHeight: [Number, String],
    minWidth: [Number, String],
    width: [Number, String],
    contain: Boolean,
    aspectRatio: String,
  },
  data: () => ({
    image: undefined,
    calculatedAspectRatio: undefined,
    naturalWidth: undefined,
    isLoading: true,
    hasError: false,
  }),
  inject: {
    vuDebug: {
      default: false,
    },
  },
  computed: {
    computedAspectRatio() {
      return Number(this.aspectRatio || this.calculatedAspectRatio);
    },
    imageSizerStyle() {
      return this.computedAspectRatio ? { paddingBottom: `${(1 / this.computedAspectRatio) * 100}%` } : undefined;
    },
    imageStyle() {
      /* eslint-disable no-restricted-globals */
      return [
        !isNaN(this.width) ? { width: `${this.width}px` } : '',
        !isNaN(this.height) ? { height: `${this.height}px` } : '',
        !isNaN(this.minHeight) ? { minHeight: `${this.minHeight}px` } : '',
        !isNaN(this.maxHeight) ? { height: `${this.maxHeight}px` } : '',
        !isNaN(this.minWidth) ? { height: `${this.minWidth}px` } : '',
        !isNaN(this.maxWidth) ? { height: `${this.maxWidth}px` } : '',
      ];
      /* eslint-enable no-restricted-globals */
    },
    imageClasses() {
      return `vu-image__image--${this.contain ? 'contain' : 'cover'}`;
    },
  },
  watch: {
    src() {
      if (!this.isLoading) this.init();
      else this.loadImage();
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (!this.lazy) {
        this.loadImage();
      }
    },
    loadImage() {
      const image = new Image();
      this.image = image;
      this.isLoading = true;

      image.onload = () => {
        /* eslint-disable prefer-template, no-useless-concat, operator-linebreak */
        if (image.decode) {
          image
            .decode()
            .catch((err) => {
              if (this.vuDebug) {
                console.warn(
                  'Failed to decode image, trying to render anyway\n\n' +
                    `src: ${this.src}` +
                    (err.message ? `\nOriginal error: ${err.message}` : ''),
                  this,
                );
                /* eslint-enable prefer-template, no-useless-concat, operator-linebreak */
              }
            })
            .then(this.onLoad);
        } else {
          this.onLoad();
        }
      };
      image.onerror = this.onError;
      image.src = this.src;

      if (!this.aspectRatio) {
        this.pollForSize(image);
      }
    },
    pollForSize(img, timeout = 100) {
      const poll = () => {
        const { naturalHeight, naturalWidth } = img;

        if (naturalHeight || naturalWidth) {
          this.naturalWidth = naturalWidth;
          this.calculatedAspectRatio = naturalWidth / naturalHeight;
          this.image = null;
        } else if (!img.complete && this.isLoading && !this.hasError && timeout != null) {
          setTimeout(poll, timeout);
        }
      };
      poll();
    },
    onLoad() {
      this.isLoading = false;
      this.$emit('load', this.src);
    },
    onError() {
      this.hasError = true;
      this.$emit('error', this.src);
    },
  },
};
</script>
<style lang="scss" scoped>
$name: 'image';

.vu-image {
  position: relative;
  overflow: hidden;
  flex: 1 0 auto;
  max-width: 100%;
  display: flex;
  background-color: var(--vu-image-background-fill-color);

  .vu-#{$name}__fill {
    flex: 1 0 0px;
    max-width: 100%;
  }

  .vu-#{$name}__sizer ~ #{$name}__fill {
    margin-right: -100%;
  }

  .vu-#{$name}__sizer {
    flex: 1 0 0px;
  }

  .vu-#{$name}__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: no-repeat;

    &--cover {
      background-size: cover;
    }

    &--contain {
      background-size: contain;
    }
  }
}
</style>
<style lang="scss">
.vu-image {
  --vu-image-background-fill-color: #f1f1f1; // $grey-2
}
</style>
