import validateAttachTarget from '../utils/validateAttachTarget';

export default {
  name: 'detachable',
  props: {
    attach: {
      default: () => false,
      validator: validateAttachTarget,
    },
    contentClass: {
      type: String,
      default: '',
    },
    contentStyle: {
      type: [String, Object],
      default: () => '',
    },
  },
  data: () => ({
    hasDetached: false,
    // the final value of renderTo
    target: null,
  }),
  watch: {
    attach() {
      this.hasDetached = false;
      this.initDetach();
    },
  },
  mounted() {
    this.initDetach();
  },
  methods: {
    initDetach() {
      if (this._isDestroyed
        || this.hasDetached
        // Leave menu in place if attached
        // and dev has not changed target
        || this.attach === '' // If used as a boolean prop (<v-menu attach>)
        || this.attach === true // If bound to a boolean (<v-menu :attach="true">)
        || this.attach === 'attach' // If bound as boolean prop in pug (v-menu(attach))
      ) return;

      let target;
      if (!this.attach) {
        // Default, detach to body
        target = document.body;
      } else if (typeof this.attach === 'string') {
        // CSS selector
        target = document.querySelector(this.attach);
      } else {
        // DOM Element
        target = this.attach;
      }

      if (!target) {
        // eslint-disable-next-line no-console
        console.warn(`Unable to locate target ${this.attach}`, this);
        return;
      }

      if (target.tagName.toLowerCase() !== 'body' && window.getComputedStyle(target).position !== 'relative') {
        // eslint-disable-next-line no-console
        console.warn(`target (${target.tagName.toLowerCase()}${target.className && ` #${target.className}`}${target.id && ` .${target.id}`}) element should have a relative position`);
      }

      // // eslint-disable-next-line no-mixed-operators
      this.target = target;
      this.hasDetached = true;
    },
  },

};
