<template>
  <div
    class="alert alert-root"
    style="visibility: visible;"
    v-if="!disabled"
  >
   <vu-message
      v-for="(message) in api._messages[namespace]"
      v-bind="message.bind"
      :key="`${message.id}`"
      @update:show="api.hide(message)"
   />
  </div>
</template>

<script>

export default {
  name: 'vu-message-container',
  props: {
    namespace: {
      type: String,
      default: 'main',
    },
  },
  created() {
    if (this.api._exists(this.namespace)) {
      this.disabled = true;
    } else {
      this.api._register(this.namespace);
    }
  },
  data: () => ({
    disabled: false,
  }),
};
</script>
<style scoped lang="css">
.alert-fade-enter, .alert-fade-leave-to {
  opacity: 0;
}

.alert-fade-enter-active, .alert-fade-leave-active {
  transition: all .5s ease-in-out;
}
</style>
