<template>
  <div class="vu-user">
    <vu-rich-user-tooltip v-if="showUserTooltip"
      :user="user"
      :disabledActions="disabledActions"
      :hiddenActions="hiddenActions"
      :attach="attach"
      v-on="listeners.vOn || {}">
      <vu-user-picture v-if="showPicture"
        :id="user.id"
        :src="user.imgSrc"
        :presence="user.presence"
        :clickable="clickable"
        :style="{ background: pictureBackground }"
        @click="(e) => $emit('click-user', value)" />
      <vu-user-name v-if="showName"
        :firstName="user.firstName"
        :lastName="user.lastName"
        :clickable="clickable"
        :shift="showPicture"
        @click="(e) => $emit('click-user', e)">
          <slot name="userName" />
      </vu-user-name>
    </vu-rich-user-tooltip>
    <template v-else>
      <vu-user-picture v-if="showPicture"
        :id="user.id" 
        :src="user.imgSrc"
        :presence="user.presence"
        :clickable="clickable"
        :style="{ background: pictureBackground }"
        @click="(e) => $emit('click-user', e)" />
      <vu-user-name v-if="showName"
        :firstName="user.firstName"
        :lastName="user.lastName"
        :clickable="clickable"
        :shift="showPicture"
        @click="(e) => $emit('click-user', e)">
          <slot name="userName"/>
      </vu-user-name>
    </template>
  </div>
</template>

<script>

import { getListenersFromAttrs } from '../../utils/vue';
import validateAttachTarget from '../../utils/validateAttachTarget';
import { validate as personValidator } from '../../model/person';
import { validate as userActionsValidator } from '../../model/userActionsValidator';

export default {
  name: 'vu-user',
  emits: ['click-other-user', 'click-user'],
  props: {
    user: {
      type: Object,
      required: true,
      validator: personValidator,
    },
    disabledActions: {
      required: false,
      validator: userActionsValidator,
    },
    hiddenActions: {
      required: false,
      validator: userActionsValidator,
    },
    showPicture: {
      type: Boolean,
      required: false,
      default: true,
    },
    showName: {
      type: Boolean,
      required: false,
      default: false,
    },
    showUserTooltip: {
      type: Boolean,
      required: false,
      default: true,
    },
    clickable: {
      type: Boolean,
      required: false,
      default: true,
    },
    pictureBackground: {
      type: String,
      required: false,
      default: '#fff',
    },
    attach: {
      default: () => false,
      validator: validateAttachTarget,
    },
  },
  computed: {
    listeners() {
      return getListenersFromAttrs(this.$attrs, true);
    },
  },
  data: () => ({
    getListenersFromAttrs,
  }),
};

</script>
<style scoped lang="scss">
  .vu-user {
    & ::v-deep(.vu-popover__activator),
    & {
      display: flex;
      align-items: center;
    }

    .vu-avatar {
      flex: 0 0 auto;
    }

    .vu-user-name {
      flex: 0 1 auto;
      height: calc(100% - 0.2em)
    }
  }
</style>
