    <span class="icon-link__link">
      <slot name="default">{{ label }} </slot>
    </span>
  </a>
</template>
<script>
import activable from '../mixins/activable';

export default {
  name: 'vu-icon-link',
  mixins: [activable],
  props: {
    label: {
      type: String,
      default: () => '',
    },
    icon: {
      type: String,
      default: () => '',
    },
  },
  data: () => ({
    pressed: false,
  }),
};
</script>

