<script setup>
// eslint-disable-next-line import/no-extraneous-dependencies
import { ref, inject, defineProps } from 'vue';
import useStickyUsingIntersection from '../composables/useStickyUsingIntersection';

const lang = inject('lang');
const el = ref(null);
const props = defineProps(['date', 'label', 'sticky', 'forceStick', 'scrollSelector']);
const { stick } = useStickyUsingIntersection(props.sticky || props.forceStick, el, 'vu-timeline-separator-date');

const prettyDate = (date) => {
  const d = new Date(date);
  const y = d.getFullYear();
  const yearNow = new Date().getFullYear();
  const options = y === yearNow ? { weekday: 'long', month: 'long', day: 'numeric' } : {
    weekday: 'long', month: 'long', day: 'numeric', year: 'numeric', 
  };

  return d.toLocaleDateString(lang, options);
};
</script>
<script>
export default {
  name: 'vu-timeline-divider',
};
</script>

<template>
  <div class="vu-timeline-separator-date" ref="el" :class="{ 'vu-timeline-separator-date--top': stick }">
    <hr />
    <div class="vu-timeline-separator-date__date">
      {{ label || prettyDate(date) }}
    </div>
    <hr />
  </div>
</template>

