<template>
  <div class="vu-tile" 
    :class="classes">
    <div class="tile-wrap">
      <div
        v-if="active"
        class="tile__thumb"></div>
      <div v-if="src"
        class="tile__image">
        <vu-image :src="src"
          width="80"
          height="60"
          contain
          aspectRatio="1"
          :lazy="vuCollectionLazyImages">
        </vu-image>
        <vu-icon
          v-if="src && (selectable || selected)"
          icon="check"
          class="tile__check" />
      </div>
      <div class="tile__content"
        :class="contentClasses">
        <div class="tile__title">
          <vu-icon v-if="type" :icon="type"></vu-icon><span class="inner">{{ title }}</span>
        </div>
        <div v-if="meta" 
          class="tile__meta"><span class="inner">{{ meta }}</span>
        </div>
        <div v-if="text"
          class="tile__text"><span class="inner">{{ text }}</span>
        </div>
      </div>
      <div class="tile__action-icon"
        v-if="_actions">
        <vu-dropdownmenu 
          v-if="_actions.length > 1"
          :items="_actions"
          @click-item="(item) => $emit('click-action', { item, id })">
          <template #default="active">
            <vu-icon-btn icon="chevron-down" :class="active"></vu-icon-btn>
          </template>
        </vu-dropdownmenu>
        <vu-icon-btn
          v-else
          :icon="_actions[0].fonticon"
          @click="(item) => $emit('click-action', { item, id })"
        />
      </div>
    </div>
    <vu-status-bar v-if="!hideStatusBar" :status="status"></vu-status-bar>
  </div>
</template>
<script>
export default {
  name: 'vu-tile',
  inject: ['vuCollectionActions', 'vuCollectionLazyImages', 'lang', 'vuTileEmphasizeText', 'vuDateFormatWeekday', 'vuDateFormatShort'],
  emits: ['click-action'],
  props: {
    id: {
      type: String, 
    },
    src: String,
    type: String,
    title: String,
    text: String,
    author: String,
    date: Date,
    customMetaData: String,
    status: Array,
    active: Boolean,
    actions: Array || String,
    selected: Boolean,
    selectable: Boolean,
    thumbnail: Boolean,
    hideStatusBar: Boolean,
  },

  computed: {
    classes() {
      return {
        'tile--selectable': this.selectable || this.selected,
        'tile--selected': this.selected,
        'tile--active': this.active,
        'tile--thumbnail': this.thumbnail,
      };
    },
    _actions() {
      return this.actions || this.vuCollectionActions;
    },
    contentClasses() {
      // Within a thumbnail -> Title can have 2 rows + Meta, or 3 rows without meta
      // Simple Tile    - Title can have 4 rows without Meta and Text
      //                - Title can be 3 rows without Meta XOR text
      //                - Title can be 2 rows with Meta and text
      //                - Title can be 1 row with Meta and 2 rows text.
      const pre = 'tile__content';
      if (this.thumbnail) {
        return this.meta ? `${pre}__title--2rows` : `${pre}__title--3rows`;
      } 
      if (this.meta && this.text) {
        return this.vuTileEmphasizeText ? [
          `${pre}__title--1row`,
          `${pre}__text--2rows`,
        ] : [
          `${pre}__title--2row`,
          `${pre}__text--1row`,
        ];
      } 
      if (this.meta ? !this.text : this.text) {
        return [`${pre}__title--3rows`, `${pre}__text--1row`];
      } 
      return `${pre}__title--4rows`;
    },
    meta() {
      return this.customMetaData || `${this.author || ''}${ this.author && this.date ? ' | ' : ''}${ this.dateFormat }`;
    },
    dateFormatOptions() {
      const options = {
        weekday: this.vuDateFormatShort ? 'short' : 'long',
        month: this.vuDateFormatShort ? 'short' : 'long',
        day: 'numeric',
        year: 'numeric',
      };
      if (!this.vuDateFormatWeekday) delete options.weekday;
      return options;
    },
    dateFormat() {
      return this.date ? this.date.toLocaleDateString(this.lang, this.dateFormatOptions) : '';
    },
  },

  data() {
    return {
      started: false,
    };
  },

  mounted() {
  },

  watch: {
  
  },

  methods: {

  },

};
</script>
<style lang="scss" scoped>
  @import '../../styles/_variables.scss';
  @import 'select';
  
  $name: 'tile';

  @include selectable(#{$name}, 'wall');
  @include active(#{$name});

  @mixin clamp($name, $height, $lines) {
    @if ($lines > 1) {
      &__#{$name}--#{$lines}rows {
        .tile__#{$name} {
          max-height: $height * $lines;
          -webkit-line-clamp: $lines;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          overflow: hidden;
        }
      }
    } @else {
      &__#{$name}--#{$lines}row {
        .tile__#{$name} {
          height: $height;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }

  .vu-#{$name} {
    --vu-tile-check-top: 9px;
    --vu-tile-check-right: 0;

    min-width: 260px;
    max-width: 540px;

    .tile-wrap {
      position: relative;
      display: flex;
    }
    
    .tile__content {
      flex: 1 1 auto;
      height: 70px;
    }

    .tile__content {
      padding-top: 5px;
      padding-left: 10px;
      padding-right: 0;
      overflow: hidden;

      @include clamp('title', 18.67px, 1);
      @include clamp('title', 18.67px, 2);
      @include clamp('title', 18.67px, 3);
      @include clamp('title', 18.67px, 4);
      @include clamp('text', 16px, 1);
      @include clamp('text', 16px, 2);
    }

    .tile__text {
      font-size: 11px;
      color: $grey-7;
    }

    .tile__meta {
      font-size: 11px;
      height: 15.714px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .tile__title {
      color: $blue-3;
      line-height: 16px;
    }

    &:not(.#{$name}--thumbnail) .tile__title { 
      margin-top: -4px;
    } 
    &.#{$name}--thumbnail .tile__content {
      padding-top: 10px;
    }

    .tile__title {
      font-size: 13px;
      color: $blue-3;

      .vu-icon {
        vertical-align: bottom;
        font-size: 16px;
        width: 20px;
        margin-left: 0;
        margin-top: -2px;
        line-height: 14px;
      }
    }

    .tile__meta {
      font-size: 11px;
      color: $grey-6;
    }

    .tile__image {
      flex: 0 0 85px;
      height: 100%;
      padding: 5px;
      padding-right: 0;
      position: relative;
    }

    .tile__action-icon {
      flex: 0 0 30px;
      align-self: center;
    }
  }

</style>
<style lang="scss">
  $name: 'tile';

  .vu-#{$name} {
    --vu-tile__thumb-top: 14px;
    --vu-tile__thumb-right: 10px;
  }
</style>
